@import "global";

//
// @variables
//

$include-html-nav-classes: $include-html-classes !default;

// We use this to control padding.
/// sidenav.globals {
$side-nav-padding: rem-calc(14 0) !default;

// We use these to control list styles.
$side-nav-list-type: none !default;
$side-nav-list-position: outside !default;
$side-nav-list-margin: 0 !default;
$side-nav-list-padding: 0 !default;

// We use these to control link styles.
$side-nav-link-color: $anchor-text-color !default;
$side-nav-link-color-active: $body-text-color !default;
$side-nav-link-color-hover: $anchor-text-color-hover !default;
$side-nav-link-bg-hover: $color-1-light !default;
$side-nav-link-margin: 0 !default;
$side-nav-link-padding: rem-calc(0 14) !default;
$side-nav-font-size: inherit !default;

// We use these to control heading styles.
$side-nav-heading-color: $color-6 !default;
$side-nav-heading-font-size: $side-nav-font-size !default;
$side-nav-heading-font-weight: bold !default;
$side-nav-heading-text-transform: uppercase !default;

// We use these to control border styles
$side-nav-divider-size: 1px !default;
$side-nav-divider-style: solid !default;
$side-nav-divider-color: $hr-border-color !default;
///}sidenav.globals

//
// @mixins
//


// We use this to style the side-nav
//
// $divider-color - Border color of divider. Default: $side-nav-divider-color.
// $font-size - Font size of nav items. Default: $side-nav-font-size.
// $link-color - Color of navigation links. Default: $side-nav-link-color.
// $link-color-hover - Color of navigation links when hovered. Default: $side-nav-link-color-hover.
@mixin side-nav(
  $divider-color:$side-nav-divider-color,
  $font-size:$side-nav-font-size,
  $link-color:$side-nav-link-color,
  $link-color-hover:$side-nav-link-color-hover,
  $link-bg-hover:$side-nav-link-bg-hover) {
  display: block;
  margin: 0;
  padding: $side-nav-padding;
  list-style-type: $side-nav-list-type;
  list-style-position: $side-nav-list-position;
  width: rem-calc(130);

  &.side-nav-border {
    padding-right: rem-calc(16);
    border-right: 1px solid $hr-border-color;
  }

  li {
    margin: $side-nav-list-margin;
    font-size: $font-size;
    padding: $side-nav-list-padding;

    a:not(.button) {
      display: block;
      color: $link-color;
      margin: $side-nav-link-margin;
      padding: $side-nav-link-padding;
      &:hover,
      &:focus {
        background: $link-bg-hover;
        color: $link-color-hover;
      }
    }

    &.active > a {
      color: $side-nav-link-color-active;
        @extend .icon-arrow-right-thin;
        //@extend .glyph-tiny;
      &:hover,
      &:focus {
        background: inherit;
        color: inherit;
        text-decoration: none;
        cursor: default;
      }
      &::before {
        float: left;
        margin-left: -1.2em;
        padding-top: .3em; // that's right, ems.
        font-family: 'snap-icons';
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
      }
    }

    &.sub-link {
      margin-left: .75rem;
    }

    &.divider {
      border-top: $side-nav-divider-size $side-nav-divider-style;
      height: 0;
      padding: 0;
      list-style-type: none;
      border-top-color: $divider-color;
    }

    &.heading {
      color: $side-nav-heading-color;
      font: {
        size: $side-nav-heading-font-size;
        weight: $side-nav-heading-font-weight;
      }
      text-transform: $side-nav-heading-text-transform;
    }
  }
}

@include exports("side-nav") {
  @if $include-html-nav-classes {
    .side-nav { @include side-nav; }
  }
}
