@import "global";

//
// @variables
//
/// labels.globals {
$include-html-label-classes: $include-html-classes !default;
$class-name: 'color-combo-4' !default;

// We use these to style the labels
$label-padding: rem-calc(4 8 4) !default;
$label-radius: $global-radius !default;

// We use these to style the label text
$label-font-sizing: rem-calc(11) !default;
$label-font-color: $color-5 !default;
$label-font-color-alt: $color-1 !default;
/// } labels.globals

//
// @mixins
//
// We use this mixin to create a default label base.
@mixin label-base {
  @extend .regular;
  text-align: center;
  text-decoration: none;
  line-height: 1;
  white-space: nowrap;
  display: inline-block;
  position: relative;
  margin-bottom: 0;
}

// @mixins
//
// We use this mixin to add label size styles.
// $padding - Used to determine label padding. Default: $label-padding || rem-calc(3 10 4) !default
// $text-size - Used to determine label text-size. Default: $text-size found in settings
@mixin label-size($padding:$label-padding, $text-size:$label-font-sizing) {
  @if $padding { padding: $padding; }
  @if $text-size { font-size: $text-size; }
}

// @mixins
//
// We use this mixin to add label styles.
// $bg - Default: $primary-color (found in settings file)
// $radius - Default: false, Options: true, sets radius to $global-radius (found in settings file)
@mixin label-style($color-class:$class-name, $bg:false, $radius:false) {
  @if $color-class {

  @extend .#{$color-class};
  }
  // We control which background color comes through
  @if $bg {

    // This find the lightness percentage of the background color.
    $bg-lightness: lightness($bg);

    background-color: $bg;

    // We control the text color for you based on the background color.
    @if $bg-lightness < 70% { color: $label-font-color-alt; }
    @else { color: $label-font-color; }
  }

  // We use this to control the radius on labels.
  @if $radius == true { @include radius($label-radius); }
  @else if $radius { @include radius($radius); }

}

// @mixins
//
// We use this to add close buttons to alerts
// $padding - Default: $label-padding,
// $text-size - Default: $label-font-sizing,
// $bg - Default: $primary-color(found in settings file)
// $radius - Default: false, Options: true which sets radius to $global-radius (found in settings file)
@mixin label($padding:$label-padding, $text-size:$label-font-sizing, $color-class:$class-name, $bg:false, $radius:false) {

  @include label-base;
  @include label-size($padding, $text-size);
  @include label-style($color-class, $bg, $radius);
}

@include exports("label") {
  @if $include-html-label-classes {
    .label {
      @include label-base;
      @include label-size;
      @include label-style;

      &.radius { @include label-style(false, false, true); }
      &.round { @include label-style(false, false, $radius:1000px); }

      &.alert         { @include label-style('color-combo-error'); }
      &.warning       { @include label-style('color-combo-attention'); }
      &.success       { @include label-style('color-combo-success'); }
      &.secondary     { @include label-style('color-combo-3'); }
      &.info          { @include label-style('color-combo-1'); }
      &.color-combo-1 { @include label-style('color-combo-1'); }
      &.color-combo-2 { @include label-style('color-combo-2'); }
      &.color-combo-3 { @include label-style('color-combo-3'); }
      &.color-combo-5 { @include label-style('color-combo-5'); }
      &.color-combo-6 { @include label-style('color-combo-6'); }
    }
  }
}
