@import "global";
@import "controls"; //for embedded
@import "../_icons";

/// icontip.globals {
$include-html-icontip-classes: $include-html-classes !default; //control the generation of css classes

$icontip-icon-style-properties: () !default;       //default properties applied to icontip icons
$icontip-icon-color: $icon-color !default;         //default color for icontips icons
$icontip-icon-color-alt: $icon-color-alt !default; //default secondary color for icontips icons
$icontip-tooltip-offset: 60% !default;             //default offset for tooltip
$icontip-icons: $icons !default;                   //default icon set to use for the icontip() mixin
$icontip-default-placement: 'south-west' !default; //default direction for the tooltip

//default CSS properties used to style an icontip/errortip tooltip
$icontip-tooltip-style-properties: (
    font-size: $font-size-small,
    font-weight: normal,
    color: $body-font-color,
    line-height: 1.6,
    background: $panel-bg-color,
    border: 1px solid $panel-border-color,
    padding: .125rem .3125rem,
  //use newlines in your text \00000A to break lines
    white-space: pre,
  //Break lines that would exceed the max-width
    word-wrap: break-word,
    max-width: 20rem
) !default;
///} globals.icontip

$tooltip-offset: $icontip-tooltip-offset;
$tooltip-offset-lr: $tooltip-offset;
$tooltip-offset-tb: $tooltip-offset;
$tooltip-default-placement: $icontip-default-placement;


$errortip-box-shadow: 0 0 4px 1px scale-color($color-6-light, $lightness: 40%) inset;
$errortip-color: $color-error;

/// icontip.mixins.icontip-base {
//Setup hovers and base rules needed for an icontip/errortip.
//
//When creating an icontip class you either need to add the icontip class
//  everywhere that class is used, or @include icontip-base() when creating the class
//
//NOTE: Internet explorer has performance problems with :hover rules that can quickly
//  degrade performance (noticeable on tables with a mid sized number of rows).
@mixin icontip-base(
  //determines tooltip placement (north-east, south-west, etc)
  $tooltip-default-placement: $tooltip-default-placement
)
/// } icontip.mixins.icontip-base
{
  //Setup the span for the icon
  display: inline-block;
  position: relative;
  cursor: $cursor-default-value;

  &:after, &>:first-child {display:none}

  //default messages, and messages in "text" attributes are displayed in ::after, otherwise use the first child for the "tooltip"
  &:hover:after, &:hover>:first-child {
    display:block;
    position: absolute;
    z-index: 1000;
  }

  //if the box is close to the screen edge, right of the icon may make more sense
  &.east {
    &::after, &>:first-child {
      left: $tooltip-offset-lr;
      right: auto;
    }
  }

  &.north {
    &::after, &>:first-child {
      top: auto;
      bottom: $tooltip-offset-tb;
    }
  }

  &.west {
    &::after, &>:first-child {
      right: $tooltip-offset-lr;
      left: auto;
    }
  }

  &.south {
    &::after, &>:first-child {
      bottom: auto;
      top: $tooltip-offset-tb;
    }
  }

  &.north-west {
    @extend .north;
    @extend .west;
  }

  &.north-east {
    @extend .north;
    @extend .east;
  }

  &.south-west {
    @extend .south;
    @extend .west;
  }

  &.south-east {
    @extend .south;
    @extend .east;
  }

  & {
    @extend .#{$tooltip-default-placement};
  }
}

/// icontip.mixins.icontip {
//Creates a custom icontip class
@mixin icontip(
  $message:null, //Default message for the icontip (can be overridden by using an attribute on the HTML element)

  //The icon to use for the icontip can be specified in multiple ways.
  //  if $icon-class is not null, it will be used ($icon-class must also be in $icons if provided)
  //  if $icon is provided it will be used (must be a map as specified in _icons.scss)
  //  if $icon-char is provided, it will be used as the font character representing the icon,
  //    $icon-font or $icon-base must also be provided
  $icon-base: null,   //If $icon-base is provided, it must be a previously defined SCSS selector
                      //  The $icon-base selector will be extended (SCSS @extend) to define the
                      //  the icon fontFamily and other icon font properties.

  $icon-font: 'snap-icons', //The fontFamily to use for the icon font.  This is only used with $icon-char.

  $icon-class: null, //The css class for the desired icon (this class must exist in the $icons map)
  $icons: $icons,    //See the icons SCSS documentation for information on how to create your
                     //  own icon set, or extend snapui's icon set.

  $icon: null,       //A map describing the desired icon, see the icons SCSS documentation for format.
  $icon-char: null,  //The character used in the icon font to identify the desired icon.

  $icon-color: $icontip-icon-color,         //Default icon color class
  $icon-color-alt: $icontip-icon-color-alt, // color to use when the color-alt class is present

  $icon-style-properties: (), //CSS properties used to style the tooltip icon

  $tooltip-style-properties: (),  //Combined with $icontip-tooltip-style-properties to style the tooltip
  $message-override-attr: 'text'  //Attribute used to override the default tooltip message
)
///}
{
  @if $icon-class {
    $icon: map-get($icons, $icon-class);
  }
  @if $icon {
    $icon-char: map-get($icon, code);
    $icon-font: map-get($icon, fontFamily);
  }
  @if $icon-color {
    & {
      color: $icon-color;
    }
  }
  @if $icon-color-alt {
    &.color-alt {
      color: $icon-color-alt;
    }
  }


  //Include icon font/char if provided
  &::before {
    @if $icon-base {
      @extend #{$icon-base};
    } @else if $icon-font {
      /*noinspection CssUnknownProperty*/
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;

      /* Better Font Rendering =========== */
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
    @each $prop, $value in map_merge($icontip-icon-style-properties, $icon-style-properties) {
      @if $value != null { #{$prop}: $value; }
    }
    @if $icon-font { font-family: $icon-font; };
    @if $icon-char  { content: $icon-char; };
  }
  & {
    @if $icon-class { @extend .#{$icon-class}; };
  }

  //Setup the "tooltip"
  & {

    //We had issues applying styles (e.g. font-size) to the first-child, so we will
    //  only style tooltips added by :after content, and leave it up to the :first-child
    //  creator to come up with selectors that IE wont ignore.
    //
    //Not sure if this belongs here or in icon-base?
    &:after {
      @each $prop, $value in map_merge($icontip-tooltip-style-properties, $tooltip-style-properties) {
        @if $value != null {
          #{$prop}: $value;
        }
      }
    }

    @if $message {
      &:not([#{$message-override-attr}])::after {
        content: $message;
      }
    }

    /* A text attribute will always override the message, null or otherwise */
    &[#{$message-override-attr}]::after {
      content: attr(#{$message-override-attr});
    }
  }
}

/// icontip.mixins.embedded-target {
//Adds CSS rules needed on the target element to embed an icontip into the element.
//
//This mixin would be included in the SASS selector used to
//  identify the element the icontip will be embedded into.
@mixin embedded-target(
  //Determines if the target element should be floated (usually left when in a grid)
  $float:left,
  //For some elements (currently 'select' & 'textarea')
  //   additional rules are needed to properly position the icontip within the element
  //   (to allow room for dropdown arrows, scrollbars, etc).
  $input-type: null
)
///} icontip.mixins.embedded-target
{
  $font-size : $input-font-size;
  $padding-tb: $control-spacing / 2;
  $padding-lr: $control-spacing / 2;
  $width: $font-size + $padding-lr * 2 + rem-calc(1);
  @if $float {
    float:$float;
  }
  padding-right: $width !important;
  @if $input-type == 'select' {
    margin-right: -1rem !important;
  }
  @if $input-type == 'textarea' {
    margin-right: -1.125rem !important;
  }
}

/// icontip.mixins.embedded-icontip {
//Adds CSS rules needed on the icontip element to embed it into another element.
//
//This mixin would be included in the SASS selector used to
//  identify the icontip.
@mixin embedded-icontip(
  //Determines if the icontip element should be floated (usually left when in a grid)
  $float:left
)
///} icontip.mixins.embedded-icontip
{
  $font-size : $input-font-size;
  $padding-tb: $control-spacing / 2 + rem-calc($input-border-width);
  $padding-lr: $control-spacing / 2;
  $width: $font-size + $padding-lr * 2 + rem-calc(1);

  @if $float {
    float:$float;
  }

  display: block;
  font-size: $font-size;
  line-height: $control-inner-height;
  &:before {
    font-size: $font-size;
    line-height: $control-inner-height;
  }
  padding: $padding-tb $padding-lr ;
  width: $width;
  margin-left: -$width;
}

@include exports("errortip-placeholders") {
  %errortip-error-holder {
    @include icontip-base();
    display: none;
  }

  %errortip-base-error-style {
      border: $field-state-border;
      box-shadow: $errortip-box-shadow;
  }

  %errortip-input-embed {
    @extend %errortip-base-error-style;

    &:not(.no-embed) {
      @include embedded-target($float:left, $input-type:'input');
    }
  }

  %errortip-select-embed {
    @extend %errortip-base-error-style;

    &:not(.no-embed) {
      @include embedded-target($float:left, $input-type:'select');
    }
  }

  %errortip-textarea-embed {
    @extend %errortip-base-error-style;

    &:not(.no-embed) {
      @include embedded-target($float:left, $input-type:'textarea');
    }
  }
}

/// errortip.mixins.embedded-icontip {
//Unlike most mixins, the errortip() mixin creates several selectors, it
//  is intended to be called from the top level instead of nested inside of
//  an existing SASS rule
@mixin errortip(
  $error-class,             //The error class to create
  $message: null,           //The default error message, can be overridden by setting
                            //  the $msg-attr attribute on the $error-holder element
  $error-holder: 'err-msg', //Class used to specify the element that will hold the icontip

  //See the icontip() mixin documentation, the common case is to use the $icon-class to specify the
  //  icon to use, or to leave the default of icon-warning-dark.
  $icons: $icontip-icons,
  $icon-class: null,
  $icon: map-get($icontip-icons, icon-warning-dark),
  $icon-char: null,
  $icon-font: 'snap-icons',

  //name of the attribute that can be added to the $error-holder element to override the error message
  $msg-attr: $error-class
)
/// }
{
  //When multiple error classes are on the same input, last one (CSS/SCSS file order) wins.
  //This only works when using $icon-char, $icon-class is resolved in the order of the icon CSS
  //so this mixin converts $icon-class to $icon-char & $icon-font to avoid that issue.

  @if $icon-class {
    $icon: map-get($icons, $icon-class);
  }
  @if $icon {
    $icon-char: map-get($icon, code);
    $icon-font: map-get($icon, fontFamily);
  }

  .#{$error-holder} { @extend %errortip-error-holder; }

  .show-errors {
    //Setup the input targets
    //TODO need to go down the list and make sure this works for all inputs, including any that may be added in the future.
    //  or limit to the ones we've checked.  Or, provide an easy way to extend (more maps?)
    input:not([type="radio"]).#{$error-class} {
      @extend %errortip-input-embed;
    }
    //TODO This works for regular selects. Need to do the same for multi-line selects
    select.#{$error-class} {
      @extend %errortip-select-embed;
    }
    textarea.#{$error-class} {
      @extend %errortip-textarea-embed;
    }
    fieldset.#{$error-class} {
      @extend %errortip-base-error-style;
      &>legend {
        background-color: $body-bg-color;
      }
    }

    //setup the icon when appearing embedded in a control
    input, textarea, select {
      &.#{$error-class} + .#{$error-holder}.embedded {
        @include icontip($message: $message, $icon-char: $icon-char, $icon-font: $icon-font, $message-override-attr: $msg-attr, $icon-color: $errortip-color);
        @include embedded-icontip($float: left);
      }
    }

    //setup the icon when appearing on it's own
    //TODO handle the way angular forms does errors, where the error classes carry up the chain of nested forms.
    //  That means a DOM parent (ng-form) could have the error class, but the error is actually on a sibling of the element we meant to check.
    .#{$error-class} .#{$error-holder}:not(.embedded) {
      @include icontip($message: $message, $icon-char: $icon-char, $icon-font: $icon-font, $message-override-attr: $msg-attr, $icon-color: $errortip-color);
      display: inline-block;
    }
  }
}

@include exports("icontip") {
  @if $include-html-icontip-classes {

    // We DO NOT WANT A HYPHEN after the word 'icon' for obvious reasons.
    // If the reasons are not obvious, you shouldn't be editing these files in the first place.
    .icontip {
      @include icontip-base();
      @include icontip();
    }

    .embedded-icontip {
      @include icontip-base();
      @include icontip();
      @include embedded-icontip();
    }

    input.embed-input {
      @include embedded-target();
    }

    select.embed-input {
      @include embedded-target($input-type:'select');
    }

    textarea.embed-input {
      @include embedded-target($input-type:'textarea');
    }

    @include errortip(validation-error);

    @if $include-angular-classes {
      @include errortip(ng-invalid); //for angular users
    }

  }
}
