@import "global";

//
// @variables
//
$include-html-panel-classes: $include-html-classes !default;

// We use these to control the background and border styles
/// panel.globals {
$panel-bg: $panel-bg-color !default;
$panel-text-color: $body-text-color !default;
$panel-link-color: $anchor-text-color !default;

$panel-border-style: null !default;
$panel-border-size: null !default;
$panel-border-color: null !default;

// We use this % to control how much we darken things on hover
$panel-function-factor: -11% !default;

// We use these to set default inner padding and bottom margin
$panel-margin-bottom: rem-calc(20) !default;
$panel-padding: rem-calc(20) !default;

$panel-header-adjust: true !default;

$callout-panel-bg: $panel-bg-color-alt !default;
$callout-panel-text-color: $color-3-light !default;
$callout-panel-link-color: $color-1-dark !default;
$callout-panel-link-color-hover: $color-white !default;
$callout-panel-border-color: null !default;
$callout-panel-link-text-decoration: $panel-callout-anchor-text-decoration !default;
///}panel.globals
//
// @mixins
//
// We use this mixin to create panels.
// $bg - Sets the panel background color. Default: $panel-pg || scale-color($white, $lightness: -5%) !default
// $padding - Sets the panel padding amount. Default: $panel-padding || rem-calc(20)
// $adjust - Sets the font color based on the darkness of the bg & resets header line-heights for panels. Default: $panel-header-adjust || true
@mixin panel($bg:$panel-bg, $border-color:$panel-border-color, $font-color: $panel-text-color, $link-color: $panel-link-color, $padding:$panel-padding, $adjust:$panel-header-adjust) {

  @if $bg {
    $bg-lightness: lightness($bg);

    @if $panel-border-style { border-style: $panel-border-style; }
    @if $panel-border-size  { border-width: $panel-border-size; }
    @if $border-color       { border-color: $border-color; }

    margin-bottom: $panel-margin-bottom;
    padding: $padding;

    background: $bg;
    color: $font-color;
    a:not(.button), a:not(.icon) {
      color: $link-color;
    }

    // Respect the padding, fool.
    &>:first-child { margin-top: 0; }
    &>:last-child { margin-bottom: 0; }

    @if $adjust {
      h1,h2,h3,h4,h5,h6,p,li,dl { color: $font-color; }

      // reset header line-heights for panels
      h1,h2,h3,h4,h5,h6 {
        line-height: 1; margin-bottom: rem-calc(20) / 2;
        &.subheader { line-height: 1.4; }
      }
    }
  }
}

@include exports("panel") {
  @if $include-html-panel-classes {

    /* Panels */
    .panel {
      @include panel;

      &.callout {
        @include panel($callout-panel-bg, $callout-panel-border-color, $callout-panel-text-color, $callout-panel-link-color);
        a {
          //text-decoration: $callout-panel-link-text-decoration;
          color: $callout-panel-link-color;
          &:hover {
            color: $callout-panel-link-color-hover;
            //color: darken($callout-panel-link-color, 15%);
          }
        }

        a.icon {  // snap-anchor-icon takes: text-color, hover-text-color, stroke-color, blur-color
          @include snap-anchor-icon($callout-panel-link-color, $callout-panel-link-color-hover); // text-color, hover-text-color, stroke-color, blur-color

          &:focus, &:hover {
            &::before {
              text-shadow: 0 0 0px;
            }
          }

        }

        .disabled a.icon, &.disabled a.icon, a.icon.disabled {
          &:focus, &:hover {
            color: $callout-panel-link-color;
          }
        }
      }

      &.radius {
        @include radius;
      }

      &.icon {
        &:before {
          position: absolute;
          color: $icon-color;
        }
        &:not([class*='glyph-']) {
          padding-left: #{$glyph-large +2.5};
          &:before {
            font-size: $glyph-large;
            margin-left: -#{$glyph-large + 1.25};
          }
        }
        &.glyph-tiny     { padding-left: #{$glyph-tiny +2.5}; &:before { margin-left: -#{$glyph-tiny + 1.25}; } }
        &.glyph-xsmall   { padding-left: #{$glyph-xsmall +2.5}; &:before { margin-left: -#{$glyph-xsmall + 1.25}; } }
        &.glyph-small    { padding-left: #{$glyph-small +2.5}; &:before { margin-left: -#{$glyph-small + 1.25}; } }
        &.glyph-medium   { padding-left: #{$glyph-medium +2.5}; &:before { margin-left: -#{$glyph-medium + 1.25}; } }
        &.glyph-large    { padding-left: #{$glyph-large +2.5}; &:before { margin-left: -#{$glyph-large + 1.25}; } }
        &.glyph-xlarge   { padding-left: #{$glyph-xlarge +2.5}; &:before { margin-left: -#{$glyph-xlarge + 1.25}; } }
        &.glyph-jumbo    { padding-left: #{$glyph-jumbo +2.5}; &:before { margin-left: -#{$glyph-jumbo + 1.25}; } }

        @if $include-compatibility-glyphsize {
          @for $size from 1 through 40 {
            &.glyph-#{$size*25} {
              padding-left: #{$size*.25 + 2.5}rem;
              &:before {
                margin-left: -#{$size*.25 + 1.25}rem;
              }
            }
          }
        }

        &.callout:before {
          color: $icon-color-alt;
        }
      }
    }
  }
}
