@import "global";

//
// @variables
//
/// accordion.globals {
$include-html-accordion-classes: $include-html-classes !default;

//Default style settings
$accordion-navigation-padding: rem-calc(12) !default;
$accordion-navigation-bg-color: $color-3 !default;
$accordion-navigation-hover-bg-color: $color-3-light !default;
$accordion-navigation-active-bg-color: $color-3 !default;
$accordion-navigation-font-color: $color-1 !default;
$accordion-navigation-font-size: rem-calc(16) !default;
$accordion-navigation-margin-bottom: rem-calc(6) !default;
$accordion-navigation-line-height: 1;
$accordion-navigation-border-top: solid 2px $color-3 !default;
$accordion-navigation-border-right: solid 2px $color-3 !default;
$accordion-navigation-border-left: solid 2px $color-3 !default;
$accordion-navigation-border-bottom: solid 2px $color-3 !default;
$accordion-navigation-text-decoration-hover: none !default;

//Alternate style settings
$accordion-navigation-padding-alt: rem-calc(4) rem-calc(12) rem-calc(4) rem-calc(2) !default;
$accordion-navigation-bg-color-alt: inherit !default;
$accordion-navigation-hover-bg-color-alt: $color-gray-20 !default;
$accordion-navigation-active-bg-color-alt: $color-gray-30 !default;
$accordion-navigation-font-color-alt: $color-5 !default;
$accordion-navigation-border-top-alt: none !default;
$accordion-navigation-border-right-alt: none !default;
$accordion-navigation-border-left-alt: none !default;
$accordion-navigation-border-bottom-alt: solid 2px $color-3 !default;

$accordion-content-padding: $column-gutter/2 !default;
$accordion-content-active-bg-color: inherit !default;
///} accordion.globals

//  Mixin: accordion-container()
//  Decription: Responsible for the container component of accordions, generating styles relating to a margin of zero and a clearfix
//  Explicit Dependencies: a clearfix mixin *is* defined.
//  Implicit Dependencies: None

@mixin accordion-container() {
  @include clearfix;
  margin-bottom: 0;
}

// COMMENTED OUT 12/31/2014 BY HARRY. This mixin wasn't being called.
//
//  Mixin: accordion-navigation( ...
//  TO SEE THIS MIXIN, CONSULT THE FOUNDATION DOCS
//

//  Mixin: accordion-content($bg, $padding, $active-class)
//    @params $padding [ number ]: Padding for the content of the container
//    @params $bg [ color  ]: Background color for the content when it's visible
//    @params $active_class [ string ]: Class name used to keep track of which accordion tab should be visible.

@mixin accordion-content($bg: $accordion-content-active-bg-color, $padding: $accordion-content-padding, $active_class: 'active'){
  display: none;
  @if type-of($padding) != "number" {
    @warn "#{$padding} was read as a #{type-of($padding)}";
    @if $accordion-content-padding != null {
      @warn "`#{$padding}` isn't a valid number. $accordion-content-padding used instead";
      padding: $accordion-content-padding;
    } @else {
      @warn "`#{$padding}` isn't a valid number and the default value of $accordion-content-padding is not defined. A value of `null` is returned to not output an invalid value for padding.";
      /*noinspection CssInvalidPropertyValue*/
      padding: null;
    }
  } @else {
    padding: $padding;
  }

  @if type-of($active_class) != "string" {
    @warn "`#{$active_class}` isn't a valid string. A valid string is needed to correctly be interpolated as a CSS class. CSS classes cannot start with a number or consist of only numbers. CSS will not be generated for the active state of the content. "
  }
  @else {
    &.#{$active_class} {
      display: block;
      background: $bg;
    }
  }
}

@include exports("accordion") {
  @if $include-html-accordion-classes {
    .accordion {
      @include clearfix;
      margin-bottom: 0;
      .accordion-navigation, dd {
        line-height: $accordion-navigation-line-height;
        display: block;
        margin-bottom: $accordion-navigation-margin-bottom !important;
        > a:hover,a:focus {
            text-decoration: $accordion-navigation-text-decoration-hover;
          }
        @if ($accordion-navigation-active-bg-color != $accordion-navigation-bg-color){
          &.active > a, &.active > .accordion-header {
            background: $accordion-navigation-active-bg-color;
          }
        }
        > a, .accordion-header {
          background: $accordion-navigation-bg-color;
          color: $accordion-navigation-font-color;
          padding: $accordion-navigation-padding;
          display: block;
          font-size: $accordion-navigation-font-size;
          &:hover {
            text-decoration: $accordion-navigation-text-decoration-hover;
            background-color: $accordion-navigation-hover-bg-color;
//            >h1, h2, h3, h4, h5, h6 { //The inherit should be enough, haven't seen anything overriding it
//              background: $accordion-navigation-hover-bg-color;
//            }
          }
          >h1, h2, h3, h4, h5, h6 {
            color: inherit;
          }
        }
        > .content {
          display: none;
          padding: $accordion-content-padding;
          &.active {
            display: block;
            background: $accordion-content-active-bg-color;
          }
        }      }
      &.alternate {
        .accordion-navigation, dd {
          background-color: $accordion-navigation-bg-color-alt;
          > a, .accordion-header {
            background: $accordion-navigation-bg-color-alt;
            border-bottom: $accordion-navigation-border-bottom-alt;
            color: $accordion-navigation-font-color-alt;
            padding: $accordion-navigation-padding-alt;
            &:focus {
              text-decoration: none;
            }
            &:hover {
              background-color: $accordion-navigation-hover-bg-color-alt;
            }
            >h1, h2, h3, h4, h5, h6 {
              color: inherit;
            }
          }
          &.active {
            @if ($accordion-navigation-active-bg-color-alt != $accordion-navigation-bg-color-alt){
              > a, .accordion-header {
                background-color: $accordion-navigation-active-bg-color-alt;
              }
            }
          }
        }
      }
    }
  }
}
