// CODE SNIPPET STYLING
.code-snippet {
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
  h6 {
    padding-top: 1rem;
  }
  .manual-tabs {
    @extend .tabs !optional;
  }

  .options-panel {
    padding: 1rem;
    background-color: $color-2-light;
    .options-heading {
      @extend .semibold;
    }
  }

  .options-toggler, .options-reset, .options-select {
    background-color: $color-2-light;
    border-left: 3px solid $color-2;
    border-bottom: 3px solid $color-2;
    padding-left: 5px;
    padding-right: 5px;
    &:hover {
      background-color: $color-2;
      text-decoration: none;
      color: inherit;
    }
  }

  .example-div {
    background-color: $color-gray-10;
    border: 3px solid $color-3;
    code {
      display: block;
      background-color: $color-gray-10;
      border: none;
      padding: 0 1rem 1rem;
    }
    & > .tabs-content {
      clear: both;
      overflow: visible;
      margin-bottom: 0px;
      .content {
        padding-bottom: 0px;
        &.html {
          @include clearfix();
          & > div {
            position: relative;
          }
          padding: .5rem 1.5rem 1.5rem;
        }
      }
    }
  }
}
