$snapAssetPath: '../../snap/assets' !default; // must be relative to the project's main CSS file

@import 'qsg/scss/generated/qsg-icons';

// To change the palette, edit the service.scss located in the same directory as this qsg.scss file.
@import 'service';


//$include-compatibility-icons: true;

// Use compatibility classes here, before the Snap UI sass gets built.
$include-compatibility-icon-bar: true;

@import '../../snap/library/scss/snap';

// Add your own SCSS code below
.qsg-content {
  @include grid-row;
  margin-top: 45px;
  background-color: $color-white;
  padding: 0 0 0 28px;
  z-index: 0;
  ul.indent {
    padding-left: 30px;
  }
}

[ng-click], [data-ng-click] {
  cursor: pointer;
}

@import '../documents/documents';
@import '../style/style';
@import '../components/components';
@import '../javascripts/code-snippet/code-snippet';


// ==========================================================================
// $ Quick Start Guide Classes
// ==========================================================================

// HOME PAGE
// Puts footer at the bottom of window OR bottom of content, whichever is lower
#wrap {
  height: 100%;
  width: 100%;
  display: table;
}

$qsg-logo: '../images/qsg-logo-' + map-get($service, 'service-name') + '.png';

.home-button { // this appears in the top-bar, far left
  height: 45px;
  width: 70px;
  margin-left: 26px;
  background: url("../images/" + map-get($service, 'service-name')+"_leaf_logo.png") no-repeat;
}

.logo-div { // this is the big logo that appears on the home page main body area
  height: 450px;
  margin-top: -22px;
  background: url($qsg-logo) no-repeat;
}

.home-page-buttons {
  @extend ul.no-bullet;
  margin-top: 78px;
}

// ARTICLE STYLING
// The article tag is the outer wrapper of (nearly) every page
article {
  position: relative;
  @include grid-column(10); //give all content a big right margin
  padding-bottom: 4rem;
  h2 {
    @extend .text-color-3;
  }
  .example-div {
    ul.circle, ul.disc, ul.square, ul.no-bullet, ol {
      margin-left: 3rem;
    }
  }
  img {
    margin-bottom: 30px;
  }
  section {
    margin-top: 3rem;
  }
  & .code-snippet {
    img {
      margin-bottom: 0;
    }
    section {
      margin-top: 0;
    }
  }
}