@import "global";
@import "../snap_styles";
@import "controls";

//
// @variables
//
/// button.globals {
$include-html-button-classes: $include-html-classes !default;
// We use these to build padding for buttons.
$button-tny: rem-calc(6) !default;
$button-xsmall: rem-calc(7) !default;
$button-sml: rem-calc(8) !default;
$button-med: rem-calc(14) !default;
$button-lrg: rem-calc(18) !default;
$button-xlarge: rem-calc(22) !default;
$button-jumbo: rem-calc(26) !default;

// We use this to control the display property.
$button-display: inline-block !default;
$button-margin-bottom: rem-calc(20) !default;

// We use these to control button text styles.
$button-font-color: $color-1 !default;
$button-font-color-alt: $color-5 !default;
$button-font-tny: $font-size-tiny !default;
$button-font-xsmall: $font-size-xsmall !default;
$button-font-sml: $font-size-small !default;
$button-font-med: $font-size-medium !default;
$button-font-lrg: $font-size-large !default;
$button-font-xlarge: $font-size-xlarge !default;
$button-font-jumbo: $font-size-jumbo !default;
$button-font-align: center !default;

// We use these to control various hover effects.
$button-function-factor: 15% !default;

// We use these to control button border styles.
$button-border-width: 1px !default;
$button-border-style: solid !default;

$button-bg-color: $color-6-dark !default;
$button-bg-hover: $color-6 !default;
$button-border-color: $button-bg-color !default;

$secondary-button-bg-color: $color-2 !default;
$secondary-button-bg-hover: $color-2-dark !default;
$secondary-button-border-color: $secondary-button-bg-color !default;

$success-button-bg-color: $color-success !default;
$success-button-bg-hover: $color-success-hover !default;
$success-button-border-color: $success-button-bg-color !default;

$alert-button-bg-color: $color-error !default;
$alert-button-bg-hover: $color-error-hover !default;
$alert-button-border-color: $alert-button-bg-color !default;

$warning-button-bg-color: $color-attention !default;
$warning-button-bg-hover: $color-attention-hover !default;
$warning-button-border-color: $warning-button-bg-color !default;

$info-button-bg-color: $color-info !default;
$info-button-bg-hover: $color-info-hover !default;
$info-button-border-color: $info-button-bg-color !default;

// We use this to set the default radius used throughout the core.
$button-radius: $global-radius !default;
$button-round: $global-rounded !default;

// We use this to set default opacity and cursor for disabled buttons.
$button-disabled-opacity: 0.4 !default;
$button-disabled-cursor: $cursor-default-value !default;

///} button.globals

//
// @MIXIN
//
// We use this mixin to create a default button base.
//
// $style - Sets base styles. Can be set to false. Default: true.
// $display - Used to control display property. Default: $button-display || inline-block

@mixin button-base($style:true, $display:$button-display) {
  @if $style {
    border-style: $button-border-style;
    border-width: $button-border-width;
    cursor: $cursor-pointer-value;
    line-height: normal;
    margin: 0 0 $button-margin-bottom;
    position: relative;
    text-decoration: none;
    text-align: $button-font-align;
    -webkit-appearance: none;
    -webkit-border-radius:0;

    //  long rules for greater specificity (need to keep hovers down for IE performance)
    &:not(.disabled):not([disabled]) {
      &:hover,
      &:focus {
        text-decoration: underline;

        &.icon {
          text-decoration: none;
          &>* {
            text-decoration: underline;
          }
        }
      }
    }

    // We can set $disabled:true to create a disabled transparent button.
    &.disabled, &[disabled] {
      text-decoration: none;
      cursor: $button-disabled-cursor;
      opacity: $button-disabled-opacity;
      box-shadow: none;
    }

  }
  @if $display { display: $display; }
}

// @MIXIN
//
// We use this mixin to add button size styles
//
// $padding - Used to build padding for buttons Default: $button-med ||= rem-calc(12)
// $full-width - We can set $full-width:true to remove side padding extend width - Default: false

@mixin button-size($padding:if($control-use-compact-settings, $button-sml, $button-med), $full-width:false) {

  // We control which padding styles come through,
  // these can be turned off by setting $padding:false
  @if $padding {
    padding-top: $padding;
    padding-#{$opposite-direction}: $padding * 2;
    padding-bottom: $padding + rem-calc(1);
    padding-#{$default-float}: $padding * 2;

    // We control the font-size based on mixin input.
    @if      $padding == $button-tny     { font-size: $button-font-tny;    @extend .glyph-tiny   }
    @else if $padding == $button-xsmall  { font-size: $button-font-xsmall; @extend .glyph-xsmall }
    @else if $padding == $button-sml     { font-size: $button-font-sml;    @extend .glyph-small  }
    @else if $padding == $button-med     { font-size: $button-font-med;    @extend .glyph-medium }
    @else if $padding == $button-lrg     { font-size: $button-font-lrg;    @extend .glyph-large  }
    @else if $padding == $button-xlarge  { font-size: $button-font-xlarge; @extend .glyph-xlarge }
    @else if $padding == $button-jumbo   { font-size: $button-font-jumbo;  @extend .glyph-jumbo  }
  }

  // We can set $full-width:true to remove side padding extend width.
  @if $full-width {
    // We still need to check if $padding is set.
    @if $padding {
    padding-top: $padding;
    padding-bottom: $padding + rem-calc(1);
    } @else if $padding == false {
      padding-top:0;
      padding-bottom:0;
    }
    padding-right: 0;
    padding-left: 0;
    width: 100%;
  }

}

// @MIXIN
//
// we use this mixin to create the button hover and border colors

// @MIXIN
//
// We use this mixin to add button color styles
//
// $bg - Primary color set in settings file. Default: $button-bg.
// $radius - If true, set to button radius which is $global-radius || explicitly set radius amount in px (ex. $radius:10px). Default: true
// $disabled - We can set $disabled:true to create a disabled transparent button. Default: false
// $bg-hover - Button Hover Background Color. Default: $button-bg-hover
// $border-color - Button Border Color. Default: $button-border-color
@mixin button-style($bg:$button-bg-color, $radius:false, $disabled:false, $bg-hover:null, $border-color:null, $is-negative: false) {

  // We control which background styles are used,
  // these can be removed by setting $bg:false
  @if $bg {

    // If no hover or border colors are specified, use scaled variants of the bg and border colors.
    @if $bg-hover == null {
      $bg-hover: if($bg == $button-bg-color, $button-bg-hover, scale-color($bg, $lightness: $button-function-factor));
    }

    @if $border-color == null {
      $border-color: if($bg == $button-bg-color, $button-border-color, scale-color($bg, $lightness: $button-function-factor));
    }

    // This finds the lightness percentage of the background color.
    $bg-lightness: lightness($bg);
    $bg-hover-lightness: lightness($bg-hover);

    //  long rules for greater specificity (need to keep hovers down for IE performance)
    &:hover, &:focus {
      &:not(:active):not(.disabled):not([disabled]) {
        & {
          background-color: $bg-hover;
          border-color: $bg-hover;
          //need to set color again to override "a" link styles
          color: if($bg-lightness > 60%, $button-font-color-alt, $button-font-color);
        }
        &.negative {
          background-color: $color-gray-30;
          border-color: $color-gray-30;
          //need to set color again to override "a" link styles
          color: if($bg-lightness > 30%, $button-font-color-alt, $button-font-color);
        }
      }
    }

    //  long rules for greater specificity (need to keep hovers down for IE performance)
    &:active, &:not(:active) {
      &:not(.disabled):not([disabled]), &.disabled, &[disabled] {
        // We control the text color for you based on the background color.
        &.negative {
          color: $color-gray-70;
          background-color: transparent;
          border-color: $color-gray-70;
        }
        color: if($bg-lightness > 60%, $button-font-color-alt, $button-font-color);
        background-color: $bg;
        border-color: $border-color;
      }
    }
  }


  // We can control how much button radius is used.
  @if $radius == true { @include radius($button-radius); }
  @else if $radius { @include radius($radius); }

}

// @MIXIN
//
// We use this to quickly create buttons with a single mixin. As @jaredhardy puts it, "the kitchen sink mixin"
//
// $padding - Used to build padding for buttons Default: $button-med ||= rem-calc(12)
// $bg - Primary color set in settings file. Default: $button-bg.
// $radius - If true, set to button radius which is $global-radius || explicitly set radius amount in px (ex. $radius:10px). Default:false.
// $full-width - We can set $full-width:true to remove side padding extend width. Default:false.
// $disabled - We can set $disabled:true to create a disabled transparent button. Default:false.
// $is-prefix - Not used? Default:false.
// $bg-hover - Button Hover Color - Default null - see button-style mixin
// $border-color - Button Border Color - Default null - see button-style mixin
// $transition - We can control whether or not to include the background-color transition property - Default:true.
@mixin button($padding:$button-med, $bg:$button-bg-color, $radius:false, $full-width:false, $disabled:false, $is-prefix:false, $bg-hover:null, $border-color:null, $transition: true) {
  @include button-base;
  @include button-size($padding, $full-width);
  @include button-style($bg, $radius, $disabled, $bg-hover, $border-color);

  @if $transition {
    @include single-transition(background-color);
  }
}


@include exports("button") {
  @if $include-html-button-classes {

    @if $include-compatibility-button {
      button {
        @extend .button;
      }
    }

    // Default styles applied outside of media query
    .button {
      @include button-base;
      @include button-size;
      @include button-style;

      @include single-transition(background-color);

      &.primary            { @include button-style}
      &.secondary          { @include button-style($bg:$secondary-button-bg-color, $bg-hover:$secondary-button-bg-hover, $border-color:$secondary-button-border-color); }
      &.success            { @include button-style($bg:$success-button-bg-color, $bg-hover:$success-button-bg-hover, $border-color:$success-button-border-color); }
      &.alert, &.error     { @include button-style($bg:$alert-button-bg-color, $bg-hover:$alert-button-bg-hover, $border-color:$alert-button-border-color); }
      &.warning            { @include button-style($bg:$warning-button-bg-color, $bg-hover:$warning-button-bg-hover, $border-color:$warning-button-border-color); }
      &.info               { @include button-style($bg:$info-button-bg-color, $bg-hover:$info-button-bg-hover, $border-color:$info-button-border-color); }

      @at-root .panel.callout {
        .button, #{if($include-compatibility-button, 'button', '')} {
          &:not(.success):not(.primary):not(.alert):not(.error):not(.warning):not(.info)
          {
            @extend .button.secondary;
          }
        }
      }

      &.jumbo  { @include button-size($padding:$button-jumbo);}
      &.xlarge { @include button-size($padding:$button-xlarge);}
      &.large  { @include button-size($padding:$button-lrg); }
      &.medium { @include button-size($padding:$button-med); }
      &.small  { @include button-size($padding:$button-sml); }
      &.xsmall { @include button-size($padding:$button-xsmall); }
      &.tiny   { @include button-size($padding:$button-tny); }
      &.input-sized { //Other input-sized settings can be found in _controls.scss.
        @include button-size($padding:$control-spacing / 2);
        margin-bottom: $control-spacing;
        text-align: center;
        line-height: $control-inner-height;
        height: $control-row-height;
      }


      &.expand { @include button-size($padding:null,$full-width:true); }

      &.left-align  { text-align: left; text-indent: rem-calc(12); }
      &.right-align { text-align: right; padding-right: rem-calc(12); }

      &.radius { @include button-style($bg:false, $radius:true); }
      &.round  { @include button-style($bg:false, $radius:$button-round); }

      &.compact { margin: 0 !important; }
    }

    .prefix .button {
      @include button-size();
    }

    //firefox 2px fix
    button.button::-moz-focus-inner {border:0; padding:0;}

    @media #{$medium-up} {
      .button {
        @include button-base($style:false, $display:inline-block);
        @include button-size($padding:false, $full-width:false);
      }
    }
  }
}
