@import "global";
@import "grid";

//
// @variables
//
$include-html-control-classes: $include-html-classes !default;
$include-html-label-classes: $include-html-control-classes !default;
$include-html-input-classes: $include-html-control-classes !default;

// We support compact and 'regular' spacing options
$control-use-compact-settings: true !default;
$control-compact-settings: (
  '$label-spacing': rem-calc(8),
  '$control-spacing': rem-calc(8),
  '$label-font-size': rem-calc(16) * .75,
  '$label-line-height': 1.5,
  '$control-spacing-height': (rem-calc(16) * .75) + rem-calc(8*2),
  '$radio-checkbox-top-margin-adjustment': 1
);

$control-spacious-settings: (
  '$label-spacing': rem-calc(16),
  '$control-spacing': rem-calc(16),
  '$label-font-size': rem-calc(14),
  '$label-line-height': 20px, //super hacky, but I was get 21px tall text, needed 20 to match inputs :>(
  '$control-spacing-height': rem-calc(14) + rem-calc(16*2),
  '$radio-checkbox-top-margin-adjustment': 3
);

// If you want to use the compact settings, use the right map.
$control-spacing-settings: if($control-use-compact-settings, $control-compact-settings, $control-spacious-settings);

// Pull out the value from the map
$label-spacing                 :map-get($control-spacing-settings, '$label-spacing');
$control-spacing               :map-get($control-spacing-settings, '$control-spacing');
$control-spacing-height        :map-get($control-spacing-settings, '$control-spacing-height');
$label-font-size               :map-get($control-spacing-settings, '$label-font-size');
$label-line-height             :map-get($control-spacing-settings, '$label-line-height');

$label-pointer: pointer !default;
$label-font-size: map-get($control-spacing-settings, '$label-font-size') !default;
$label-line-height: 1.5 !default;
$label-font-color: $color-5 !default;
$label-small-transform: capitalize !default;
$label-bottom-margin: 0 !default;

$input-font-family: inherit !default;
$input-font-color: $color-5 !default;
$input-font-size: $label-font-size !default;
$input-bg-color: $color-gray-10 !default;
$input-focus-bg-color: $color-white !default;
$input-border-color: $color-gray-30 !default;
$input-focus-border-color: $color-gray-60 !default;
$input-border-style: solid !default;
$input-border-width: 1px !default;
$input-border-radius: $global-radius !default;
$input-disabled-bg: $color-gray-30 !default;
$input-disabled-border-color: $input-border-color !default;
$input-disabled-cursor: $cursor-default-value !default;
$input-box-shadow: 0 0 2px $color-gray-20 !default;
$input-include-glowing-effect: true !default;
// We use this setting to turn on/off HTML5 number spinners (the up/down arrows)
$input-number-spinners: true !default;

// We use this to style the glowing effect of inputs when focused
$glowing-effect-fade-time: $global-hover-transition-time !default;
$glowing-effect-color: $input-focus-border-color !default;

// Select variables
$select-font-family: inherit !default;
$select-bg-color: $color-gray-10 !default;
$select-hover-bg-color: $color-white !default;

$control-line-height: 1.2 !default;
$control-row-height: $control-spacing-height;
$control-inner-height: $control-row-height - $control-spacing - rem-calc($input-border-width) * 2;

$label-stacked-height: $control-inner-height;

$radio-top-adjustment-px: 1px;
$radio-checkbox-top-margin-adjustment: map-get($control-spacing-settings, '$radio-checkbox-top-margin-adjustment');

// We want radio/checkbox controls to be the same height as text-type inputs. BUT, they don't take padding, for example. So we calculate their bottom margin
// using this formula.
// Row height (for compact it's 28px) minus radio/checkbox native height (13px), minus the top padding (4px), plus our standard bottom margin we use for text-type of inputs (8px). THEN...
// to make it 'compact', we remove that bottom margin amount (8 px)
$radio-native-height: rem-calc(13);
$radio-top-margin: $control-spacing / 2 + rem-calc($radio-checkbox-top-margin-adjustment); //the adjustment is to make the baseline match labels.
$radio-bottom-margin: $control-row-height - $radio-native-height - $radio-top-margin + $control-spacing;


// @MIXIN
//
// We use this mixin to create form labels
//
// $alignment - Alignment options. Default: false. Options: [right, inline, false]
// $base-style - Control whether or not the base styles come through. Default: true.
@mixin label($alignment:false, $base-style:true) {

  // Control whether or not the base styles come through.
  @if $base-style {
    font-size: $label-font-size;
    :not(.button) {
      color: $label-text-color;
    }
    cursor: $label-pointer;
    display: block;
    @extend .semibold;
    line-height: $label-line-height;
    margin-bottom: $label-bottom-margin;
  }

  // Alignment options
  @if $alignment == right {
    float: none !important;
    text-align: right;
  }
  @else if $alignment == inline {
    margin: 0 0 $label-spacing 0;
    padding: strip-unit($label-spacing / 2) * $rem-base + $input-border-width 0;
  }
}

// @MIXIN
//
// We use this mixin to give all basic form elements their style
@mixin form-element {
  background-color: $input-bg-color;
  font-family: $input-font-family;
  border: {
    style: $input-border-style;
    width: $input-border-width;
    color: $input-border-color;
  }
  line-height: $control-line-height;
  box-shadow: $input-box-shadow;
  color: $input-font-color;
  display: block;
  font-size: $input-font-size;
  margin: 0 0 $control-spacing 0;
  padding: $control-spacing / 2;
  height: $control-row-height;
  width: 100%;
  @include box-sizing(border-box);
  @if $input-include-glowing-effect {
    @include block-glowing-effect(focus, $glowing-effect-fade-time, $glowing-effect-color);
  }
  // Basic focus styles
  &:focus, &:hover {
    background-color: $input-focus-bg-color;
    border-color: $input-focus-border-color;
    outline: none;
  }
  // Disbaled Styles
  &:disabled {
    background-color: $input-disabled-bg;
    border-color: $input-disabled-border-color;
    cursor: $input-disabled-cursor;
  }

  // Disabled background input background color
  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    background-color: $input-disabled-bg;
    border-color: $input-disabled-border-color;
    cursor: $input-disabled-cursor;
  }
}

// We use this mixin to style select elements
@mixin snap-select  {
  -webkit-border-radius: 0px;
  background-color: $select-bg-color;

  // Hide the dropdown arrow shown in newer IE versions
  -webkit-appearance: none !important;
  -moz-appearance: none;
  appearance: none;
  &::-ms-expand {
    display: none;
  }

  // The custom arrow have some fake horizontal padding so we can align it
  // from the right side of the element without relying on CSS3
  background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZlcnNpb249IjEuMSIgeD0iMTJweCIgeT0iMHB4IiB3aWR0aD0iMjRweCIgaGVpZ2h0PSIzcHgiIHZpZXdCb3g9IjAgMCA2IDMiIGVuYWJsZS1iYWNrZ3JvdW5kPSJuZXcgMCAwIDYgMyIgeG1sOnNwYWNlPSJwcmVzZXJ2ZSI+PHBvbHlnb24gcG9pbnRzPSI1Ljk5MiwwIDIuOTkyLDMgLTAuMDA4LDAgIi8+PC9zdmc+);

  // We can safely use leftmost and rightmost now
  background-position: if($text-direction == 'rtl', 0%, 100%) center;

  background-repeat: no-repeat;
  border: {
    style: $input-border-style;
    width: $input-border-width;
    color: $input-border-color;
  }
  padding: $control-spacing / 2;
  padding-right: 1.25rem; //making room for the pip
  font-size: $input-font-size;
  font-family: $select-font-family;
  color: $input-font-color;
  height: $control-row-height;
  line-height: $control-line-height;
  @include radius(0);
  &.radius { @include radius($global-radius); }
  &:hover {
    background-color: $select-hover-bg-color;
    border-color: $input-focus-border-color;
  }
  // Disabled Styles
  &:disabled {
    background-color: $input-disabled-bg;
    border-color: $input-disabled-border-color;
    cursor: $input-disabled-cursor;
  }
}

@mixin input-set() {
  @include grid-row('nest-collapse');
  list-style-type: none;
  line-height: inherit;
  li {
    @include grid-column($collapse: true, $last-column: true);
    line-height: inherit;
  }
  &:not(.no-padding) li:not(:first-child):not(.no-padding) {
    padding-left: $control-spacing / 2;
  }
}

@mixin radio-checkbox-set() {
  &>li label {
    padding: 0 1.625rem 0 0; // add a little right padding to radios and checkboxes so they have adequate space between them.
  }
  &[class*="block-grid-"] > li {
    padding: 0;
  }
  @include grid-row('nest-collapse');
  list-style-type: none;
  &.vertical {
    li {
      display: block;
      width: 100%;
    }
  }

  margin-bottom: $control-spacing;

  &.compact>li>label {
    &>input[type="radio"],input[type="checkbox"] {
      // the standard radio/checkbox bottom margin, minus 8px. That makes it compact.
      margin-bottom: $control-spacing / 2; //$radio-bottom-margin - $control-spacing;
    }
  }

  li {
    @include grid-column($collapse: true);
  }
}


// We use this mixin to turn on/off HTML5 number spinners
@mixin html5number($browser, $on:true) {
  @if $on==false {
    @if $browser==webkit {
      -webkit-appearance: none;
      margin: 0;
    } @else if $browser==moz {
      -moz-appearance: textfield;
    }
  }
}

@include exports("labels") {
  @if $include-html-label-classes {
    label {
      display: block;
      width: 100%;
    }

    /* Label Styles */
    %label-base-style {
      @include label;
      &.right {
        @include label(right, false);
      }
      &.inline {
        @include label(inline, false);
      }
      small {
        text-transform: $label-small-transform;
        color: $label-text-color; //              COLOR
      }
    }

    .wrap-inputs > label, label.wrap-input {
      @include clearfix();
    }

    .wrap-inputs > label > :first-child {
      @extend %label-base-style;
    }
    :not(.wrap-inputs) > label {
      &:not(.wrap-input), &.wrap-input > :first-child {
        @extend %label-base-style;
      }
    }

    /* Styles for required inputs */
    %required-style {
      content: "*";
      color: $color-required;
    }
    .wrap-inputs > label.required > :first-child:after {
      @extend %required-style;
    }
    :not(.wrap-inputs) > label.required {
      &:not(.wrap-input):after, &.wrap-input > :first-child:after {
        @extend %required-style;
      }
    }
  }
}

@include exports("inputs") {
  @if $include-html-input-classes {
    /* We use this to get basic styling on all basic form elements */

    %snap-decision-text { // This is for inputs, selects, text areas, etc.
      &:hover {
        background-color: $color-white;
        border: 1px $color-gray-60 solid;
      }
      height: 6rem;
      -webkit-border-radius: 0px;
      @include form-element;
      @if $input-include-glowing-effect == false {
        @include single-transition(all, $global-hover-transition-time, linear);
      }
      &.radius {
        @include radius($input-border-radius);
      }
    }
    %snap-decision-push { // This is for radios and checkboxes (i.e. controls that you push, not type in).
      margin-right: $control-spacing / 2;
      margin-top: $radio-top-margin; //top margin helps align our baseline with other labels
      margin-bottom: $control-spacing / 2; //default is shorter than other controls

      //Adjustment to make radio/checkboxes line up better with their text
      position: relative;
      top: $radio-top-adjustment-px;
    }

    //make radio/checkboxes the same height as other controls.
    .input-sized { //Input-sized buttons are handled in _buttons.scss
      //directly on the radio/checkbox
      &[type="radio"], &[type="checkbox"] {
        margin-bottom: $radio-bottom-margin;
      }

      //parent (most likely the label) has .input-sized class
      >input[type="radio"], >input[type="radio"] {
        margin-bottom: $radio-bottom-margin;
      }

      //input-sized on a radio-set/checkbox-set ul
      &.radio-set, &.checkbox-set {
        margin-bottom: 0;
        >li {
          input[type="radio"], input[type="radio"] {
            margin-bottom: $radio-bottom-margin;
          }
        }
      }
    }

    %snap-input{
      @each $value in $input-types-text {
        &[type="#{$value}"] {
          @extend %snap-decision-text;
        }
      }
      @each $value in $input-types-push {
        &[type="#{$value}"] {
          @extend %snap-decision-push;
        }
      }
    }
    input {
      @extend %snap-input;
    }
    select,
    textarea {
      @extend %snap-decision-text;
    }

    input[type="submit"] {
      -webkit-appearance: none;
      -webkit-border-radius: 0;
    }

    select[multiple], textarea {
      height: $control-row-height * 5;
    }

    /* Respect enforced amount of rows for textarea */
    textarea[rows], select[size] {
      height: auto;
    }

    /* Not allow resize out of parent */
    textarea {
      max-width: 100%;
    }

    /* Add height value for select elements to match text input height */
    select {
      @include snap-select;
      &[multiple] {
        background-image: none;
        padding-top: 0;
        padding-bottom: 0;
        padding-right: 0;
      }
    }

    /* Adjust margin for form elements below */
    input[type="file"],
    select {
      margin: 0 0 $control-spacing 0;
    }

    /* Normalize file input width */
    input[type="file"] {
      width:100%;
    }

    /* HTML5 Number spinners settings */
    input[type="number"] {
      @include html5number(moz, $input-number-spinners)
    }
    input[type="number"]::-webkit-inner-spin-button,
    input[type="number"]::-webkit-outer-spin-button {
      @include html5number(webkit, $input-number-spinners);
    }
  }

  ul.input-set {
    @include input-set();
  }
  ul.radio-set, ul.checkbox-set {
    @include radio-checkbox-set();
    &.compact > li > label {
        padding-bottom: 0;
    }
  }
}