///* ==========================================================================
//   $ OPEN SANS (regular, bold and italic)
//   ========================================================================== */

@import "generated/_open_sans_fonts.scss";
@import "components/_global";

$files: () !default;

/***** Font Definition for Open Sans. This stylesheet comes from qrohlf.com/posts/better-opensans *****/

/* Regular */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-Regular-webfont.woff', $files)) format('woff');
  font-weight: normal;
  font-weight: 400;
  font-style: normal;

}

/* Italic */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-Italic-webfont.woff', $files)) format('woff');
  font-weight: normal;
  font-weight: 400;
  font-style: italic;

}

/* Light */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-Light-webfont.woff', $files)) format('woff');
  font-weight: 200;
  font-style: normal;

}

/* Light Italic */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-LightItalic-webfont.woff', $files)) format('woff');
  font-weight: 200;
  font-style: italic;

}

/* Semibold */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-Semibold-webfont.woff', $files)) format('woff');
  font-weight: 500;
  font-style: normal;

}

/* Semibold Italic */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-SemiboldItalic-webfont.woff', $files)) format('woff');
  font-weight: 500;
  font-style: italic;

}

/* Bold */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-Bold-webfont.woff', $files)) format('woff');
  font-weight: bold;
  font-weight: 700;
  font-style: normal;

}

/* Bold Italic */
@font-face {
  font-family: 'Open Sans';
  src: url(data_url('fonts/opensans/OpenSans-BoldItalic-webfont.woff', $files)) format('woff');
  font-weight: bold;
  font-weight: 700;
  font-style: italic;

}

/* Extra Bold */
@font-face {
  font-family: 'Open Sans';
  src:  url(data_url('fonts/opensans/OpenSans-ExtraBold-webfont.woff', $files)) format('woff');
  font-weight: 900;
  font-style: normal;

}

/* Extra Bold Italic */
@font-face {
  font-family: 'Open Sans';
  src:   url(data_url('fonts/opensans/OpenSans-ExtraBoldItalic-webfont.woff', $files)) format('woff');
  font-weight: 900;
  font-style: italic;

}
/* Condensed */
@font-face {
  font-family: 'Open Sans Condensed';
  src: url(data_url('fonts/opensans/OpenSans-CondLight-webfont.woff', $files)) format('woff');
  font-weight: 400;
  font-style: normal;

}

/* Condensed Bold */
@font-face {
  font-family: 'Open Sans Condensed';
  src: url(data_url('fonts/opensans/OpenSans-CondBold-webfont.woff', $files)) format('woff');
  font-weight: 700;
  font-style: normal;

}


