
@import "global";
/* Stylesheet for Tigra Calendar v5.0
* Product is Public Domain (Free for any kind of application, customization and derivative works are allowed)
* URL: http://www.softcomplex.com/products/tigra_calendar/
*/
.tcalInput {
  background: url("data:image/gif;base64,R0lGODlhEQAPAKIGAP///9bT1qWmpUpNSkpNpaVNSgAAAAAAACH5BAEAAAYALAAAAAARAA8AAANCKLrcY4LISZp6kUorMADBF4Li55HoOGJB67ZFXLSn+hW3CZUoDvisl0s2C9R4NsAxxQsKn8Ydc7pMrgyDrHbLfSQAADs=") 100% 50% no-repeat;
  padding-right: 20px;
  cursor: pointer;
  &::-ms-clear {
    display: none;
  }
}

input[readonly].tcalInput, input[disabled].tcalInput {
  background-image: none;
}

.tcalActive {
  background-image: url("data:image/gif;base64,R0lGODlhEQAPAKIGAP///9bT1qWmpUpNSkpNpaVNSgAAAAAAACH5BAEAAAYALAAAAAARAA8AAANCKLrcY4LISZp6kUorMADBF4Li55HoOGJB67ZFXLSn+hW3CZUoDvisl0s2C9R4NsAxxQsKn8Ydc7pMrgyDrHbLfSQAADs=");
}

#tcal {
  position: absolute;
  visibility: hidden;
  z-index: 1006; /* needs to be above reveal modal */
  width: 246px;
  background-color: $color-white;
  margin-top: -1px;
  padding: 0 2px 2px 2px;
  border: 1px solid $color-gray-20;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.1); }

#tcalControls, #tcalGrid {
  margin-bottom: 0;
  border-collapse: collapse; }

#tcalControls {
  border-collapse: collapse;
  border: 0;
  width: 100%; }

#tcalControls td {
  padding: 0;
  width: 16px;

  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-color: $color-white;
  cursor: pointer; }

#tcalControls th {
  padding: 0;
  line-height: 30px;
  font-size: 0.875em;
  text-align: center;
  font-weight: bold;
  white-space: nowrap;
  background-color: $color-white; }

#tcalPrevYear {
  background-image: url("data:image/gif;base64,R0lGODlhCQAIAJEAAKXD2aLC2UiGs////yH5BAAAAAAALAAAAAAJAAgAAAIWnGF3ksDOBoRP2GAZtmMb30VhEiRdAQA7"); }

#tcalPrevMonth {
  background-image: url("data:image/gif;base64,R0lGODlhBAAIAJEAAKXD2aLC2UiGs////yH5BAAAAAAALAAAAAAEAAgAAAIMnHEyIMIdmopqRGMLADs="); }

#tcalNextMonth {
  background-image: url("data:image/gif;base64,R0lGODlhBAAIAJEAAKXD2aLC2UiGs////yH5BAAAAAAALAAAAAAEAAgAAAINxC42ksvARGBh1XFHAQA7"); }

#tcalNextYear {
  background-image: url("data:image/gif;base64,R0lGODlhCQAIAJEAAKXD2aLC2UiGs////yH5BAAAAAAALAAAAAAJAAgAAAIXxD52osP73BPUUHaDFIFqqXGIOHyHWQAAOw=="); }

#tcalGrid {
  border-collapse: collapse;
  border: 1px solid $color-gray-20;
  width: 100%; }

#tcalGrid th {
  border: 1px solid $color-gray-20;
  border-collapse: collapse;
  padding: 3px 0;
  text-align: center;
  font-size: 0.875em;
  background-color: $color-4;
  color: $color-1; }

#tcalGrid td {
  border: 0;
  color: $body-text-color;
  border-collapse: collapse;
  padding: 2px 0;
  text-align: center;
  width: 14%;
  font-size: 0.875em;
  cursor: pointer; }

#tcalGrid td.tcalOtherMonth {
  color: $color-gray-30; }

#tcalGrid td.tcalWeekend {
  background-color: $color-gray-50; }

#tcalGrid td.tcalToday {
  border: 1px solid $body-text-color; }

#tcalGrid td.tcalSelected {
  background-color: $color-2; }
