@import "functions";

/// globals.icons.$icons {
//$icons will hold the icons snap knows about as a map
//Format:
//  (
//  .....
//  "icon-class":(code:<char-code>,fontFamily: <icon-font-family>, iconPrefix: "icon-", class: "icon-class)
//  .....
//  )
$icons: () !default;
//}

/// globals.icons.$icons-font-list {
$icons-font-list: () !default;
//}
$include-html-icon-classes: true !default;
$include-compatibility-icons: false !default;

/// mixins.icons.map-help.icon {
//Retrieves the map of properties for the given icon class.
@function icon(
  $icon-class,  //The class of the icon (e.g. icon-book)
  $icons:$icons //The map to search in, defaults to the global icon map
)
  /// }
{
  @if map-has-key($icons, $icon-class) {
    @return map-get($icons, $icon-class);
  } @else {
    @warn $icon-class + " - Icon not found";
    @return null;
  }
}

/// mixins.icons.map-help.icon-code {
//Retrieves the character code used to represent the given icon class.
@function icon-code(
  $icon-class,  //The class of the icon (e.g. icon-book)
  $icons:$icons //The map to search in, defaults to the global icon map
)
  /// }
{
  @return map-get(icon($icon-class, $icons), code);
}

/// mixins.icons.map-help.icon-family {
//Retrieves the font family (e.g. 'snap-icons') used to represent the given icon class.
@function icon-family(
  $icon-class,  //The class of the icon (e.g. icon-book)
  $icons:$icons //The map to search in, defaults to the global icon map
)
  /// }
{
  @return map-get(icon($icon-class, $icons), fontFamily);
}

/// mixins.icons.map-help.icon-font {
//Retrieves the map of properties used to define the given font family.
@function icon-font(
  $icon-font-family,                   //font family (e.g. 'snap-icons')
    $icons-font-list: $icons-font-list //The map to search, defaults to the global icon font list
)
  /// }
{
  @if map-has-key($icons-font-list, $icon-font-family) {
    @return map-get($icons-font-list, $icon-font-family);
  } @else {
    @warn $icon-font-family + " - Icon family not found";
    @return null;
  }
}

@function toFontURL($file, $mimeType, $font-dir, $cacheBuster) {
  @if (str_index($file, 'base64,') == 1) {
    @return 'data:' + $mimeType + ';charset=utf-8;' + $file;
  } @else {
    @return $font-dir + $file + if($cacheBuster, '?' + $cacheBuster, '');
  }
}

/// mixins.icons.gen.generate-icon-font-face {
//Creates the CSS @font-face rules needed to define the icon font, and
//  a SCSS %font-base-<icon-prefix> placeholder for icon classes to extend.
//
//NOTE: For fonts in the global $icons-font-list map, this is called as part of the snap
//  compilation (see generate-icon-sets).
@mixin generate-icon-font-face(
  $icon-font, //The map of properties defining the icon font
  $font-dir: map-get($icon-font, fontDir), //Optionally override the font directory in the properties
  $compatibility-icons-selector: $include-compatibility-icons //generate [class..] rules instead of .icon
)
  /// }
{
  $cacheBuster: map-get($icon-font, cacheBuster);
  @if $cacheBuster {
    $cacheBuster: '-' + $cacheBuster;
  }

  @font-face {
    font-family: map-get($icon-font, fontFamily);
    src: url(toFontURL(map-get($icon-font, 'woff'), 'application/x-font-woff', $font-dir, $cacheBuster)) format('woff');
    font-weight: normal;
    font-style: normal;
  }

  %font-base-#{map-get($icon-font, iconPrefix)} {
    font-family: map-get($icon-font, fontFamily);
    /*noinspection CssUnknownProperty*/
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  @if $compatibility-icons-selector {
    [class^="#{map-get($icon-font, iconPrefix)}"], [class*=" #{map-get($icon-font, iconPrefix)}"] {
      @extend .icon;
    }
  }
}

/// mixins.icons.gen.generate-icon-classes {
//Creates the CSS rules needed to define the given icon classes.
//
//NOTE: For icons in the global $icons map, this is called as part of the snap
//  compilation (see generate-icon-sets).
@mixin generate-icon-classes(
  $icons, //Map of icons to create
  $fontFamily: null //If not null, only icons in the given font family will be created.
)
  /// }
{
  @each $icon-name, $icon in $icons {
    @if $fontFamily == null or $fontFamily == map-get($icon, fontFamily) {
      .#{$icon-name}:before {
        content: map-get($icon, code);
        @extend %font-base-#{map-get($icon, iconPrefix)}
      }
    }
  }
}

/// icon transformations
.spin {
  -webkit-animation: spin 2s infinite linear;
  animation: spin 2s infinite linear;
  display:inline-block;
  line-height: 1;
  min-width: 0;
}
.spin.reverse {
  -webkit-animation: spin-reverse 2s infinite linear;
  animation: spin-reverse 2s infinite linear;
}

.icon-list-icon.icon-spin{
  min-width: 0;
  line-height: 1;
}
@-webkit-keyframes icon-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@keyframes spin-reverse {
  0% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

/// mixins.icons.gen.generate-icon-sets {
//Calls both generate-icon-font-face & generate-icon-sets to create the CSS
//  needed for the given icons and icon fonts.
//
//NOTE: If $include-html-icon-classes is true (default) this will be called
//  as part of including icons.scss for the global $icons & $icons-font-list
//  maps.
@mixin generate-icon-sets(
  $icons,  //map of icons
  $icon-font-list, //map of fonts
  $compatibility-icons-selector: $include-compatibility-icons //generate [class..] rules instead of .icon
)
  /// }
{
  @each $font-name, $icon-font in $icons-font-list {
    @include generate-icon-font-face($icon-font, map-get($icon-font, fontDir), $compatibility-icons-selector);
    @include generate-icon-classes($icons, map-get($icon-font, fontFamily));
  }
}

@include exports("icon-classes") {
  @if $include-html-icon-classes {
    @include generate-icon-sets($icons, $icons-font-list);
  }
}
