@import "global";
@import "../snap_styles";
@import "top-bar";
@import "../generated/_snap_images.scss";
//
// @variables
//
$include-html-masthead-classes: $include-html-classes !default;
//Must be an 83w x 86h pix image, path should be relative to the location your compiled CSS is served from.
$masthead-image-bg-url: url(data_url('img/map-banner-' + map-get($service, 'service-name') + '.jpg', $files)) !default;
$masthead-image-url: url(data_url('img/' + map-get($service, 'service-name') + '-logo.png', $files)) !default;

//for now heights are hardcoded/non configurable.
$masthead-topbar-height: 50px;
$masthead-height-mini: 30px;
$top-stripe-height: 36px;
$banner-height: 75px;
$masthead-bottom-spacer: 20px;

@include exports("masthead") {
  @if $include-html-masthead-classes {

    .masthead-container {
      &.hide-header-toggle .mini-toggle {
        display: none
      }
      top: 0;
      left: 0;
      height: $top-stripe-height + $topbar-height + $banner-height;
      margin-bottom: $masthead-bottom-spacer;

      a:not(.button) .icon:hover, a:not(.button) .icon:focus
      {
        &::before { //only this specific icon class, not all other widgets that can include an icon
          @include icon-shadow($color-white, $anchor-icon-color);
        }
      }
      &.mini {
        height: $masthead-height-mini;
        .icon-label, label {
          display: none;
        }
        .masthead {
          &:after {
            content: "";
            background: white $masthead-image-bg-url repeat-x;
            opacity: .375;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            position: absolute;
            z-index: -1;
          }

          height: $masthead-height-mini + 1px; // For the border
          .top-bar-container {position: relative;}
          .top-bar {
            @include top-bar-config($bg-color: none, $color: $color-5, $menu-line-height:$masthead-height-mini);

            line-height: $masthead-height-mini;
            height: $masthead-height-mini;
            top: -($masthead-topbar-height / 2) - ($masthead-height-mini / 2) + 4px; // For the position of the topbar
            left: 0px;
            position: absolute;

            @include grid-column(8);
            padding: 0px;

            background: none;
            .top-bar-section ul {
              background: inherit;
              li {
                background: inherit;
              }
            }
          }
          section.top-bar-section > ul > li {
            &:not(.has-form) > a:not(.button) {
              padding-left: 10px;
              padding-right: 10px;
            }
            a {
              line-height: 29px;
              background: none;
              color: black;
              &:not(.button):hover {
                background: lighten($color-4, 50%);
              }
            }
            ul > li > a {
              line-height: 12px;
              background: lighten($color-4, 60%);
              &:not(.button):hover {
                background: lighten($color-4, 50%);
              }
            }
            &:hover {
              background: lighten($color-4, 55%);
            }
          }

          .has-dropdown > a:after {
            top: 15px;
          }

          .top-stripe {
            padding-top: 0;
            background: none;
            color: $color-5;
            .home-logo {
              display: none;
            }
            .user-info {
              display: none;
            }
            .mini-container {
              margin-top: 0;
              padding-top: .35rem;
              @include grid-column(1);
              .mini-toggle {
                //@include snap-anchor-icon();
                //@extend .glyph-small;
                //@extend .icon-arrow-down;
                @include snap-anchor-icon($color-6, $color-6-light); // text-color, hover-text-color, stroke-color, blur-color

                &:focus, &:hover {
                  &::before {
                    text-shadow: 0 0 0px; //default is a little much
                  }
                }
                @extend .glyph-small;
                @extend .icon-arrow-down;
              }
            }
            .classification {
              padding-top: .2rem;
              @include grid-column(1);
            }
            .icons {
              padding-top: .35rem;
              position: static;
              @include grid-column(10);
              .icon {
                margin: 0;
                @extend .glyph-small;
              }
            }
          }
          .banner {
            display: none;
          }
        }
      }

      .masthead {
        top: 0;
        left: 0;
        position: fixed;
        width: 100%;
        z-index: 9;
        background-color: $color-white;
        border-bottom: 1px solid $color-4;
        section {
          margin-top: 0;
        }

        .top-stripe {
          > div {
            position: relative;
          }
          padding-top: 0.5rem;
          line-height: 1.5;
          height: $top-stripe-height;
          background-color: $color-4;
          color: $color-1;
          div > div:last-child {
            padding-right:1rem;
          }
          a {
            color: inherit;
            background: inherit;
          }
          .icon-label, label {
            margin: 0;
            background-color: rgba(255, 255, 255, .5);
            @extend .font-size-xsmall;
            padding: 0;
            text-align: center;
            color: #b34d39;
            font-size: 60%;
          }

          .icons {
            position: absolute;
            @include grid-column(6);
            top: $top-stripe-height + $topbar-height + 6px ; // Extra pixels come from top-stripe padding.
            right: 0;
            z-index: 90; //must be lower than menu, etc
            display: inline-block;
            text-align:center;
            .icon {
              margin: 0 .3rem 0;
              text-align: center;
              display:inline-block;
              @include snap-anchor-icon(); //why do we need this?  what's overriding the default?
              @extend .glyph-large;
            }
            &.disabled {
              opacity: 0.5 !important;
              cursor: not-allowed !important;
              & .icon::before {
                text-shadow: none !important;
              }
            }
          }
          .classification {
            @include grid-column(6);
          }
          .mini-container {
            margin-top: -.15rem;
            @include grid-column(1);
            .mini-toggle {
              @include snap-anchor-icon($color-2, $color-white); // text-color, hover-text-color, stroke-color, blur-color

              &:focus, &:hover {
                 &::before {
                   text-shadow: 0 0 0px; //default is a little much
                 }
               }
              @extend .glyph-small;
              @extend .icon-arrow-up;
            }
          }
        }

        .top-bar {
          @include grid-column(11);
          @include top-bar-config($bg-color: $color-white, $color: $color-5, $menu-line-height: $masthead-topbar-height);
          padding: 0;
          line-height: $masthead-topbar-height;
          height: $masthead-topbar-height;
        }

        .user-info {
          @include grid-column(4);
        }

        .home-logo {
          @include grid-column(1);
          background: $masthead-image-url no-repeat;
          padding-left: 0;
          margin-top: -8px;
          a {
            width   : 83px;
            height  : 86px;
            display : block;
          }
        }

        // The wall-to-wall map image at the top of all pages BUT the home page
        .banner {
          position: relative;
          height: $banner-height;
          border-top: 1px solid $color-4;
          &:after {
            content: "";
            background: white $masthead-image-bg-url repeat-x;
            opacity: .375;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            height:100%;
            width:100%;
            position: absolute;
            z-index: -1;
          }
        }
      }
    }
  }
}
