/*
 * Modified to use snap colors, snap icons, etc
 */

@import "global";

.toast-title {
  font-weight: bold;
}
.toast-message {
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

// If a toaster message is longer than two lines, this keeps it from
//  "flowing" under the icon on the toast.
.toast-message, .toast-title {
  margin-left:2.9rem;
}

@mixin style_elements($alt-scheme: false) {
  .toast-message, .toast-title {
    a, label {
      color: if($alt-scheme, $anchor-icon-color-alt, $anchor-icon-color);
    }
    a:hover {
      color: if($alt-scheme, $anchor-icon-color-hover-alt, $anchor-icon-color-hover);
    }
  }
}

.toast-close-button {
  position: relative;
  right: -0.3em;
  top: -0.3em;
  float: right;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  -webkit-text-shadow: 0 1px 0 #ffffff;
  text-shadow: 0 1px 0 #ffffff;
  opacity: 0.8;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  filter: alpha(opacity=80);
}
.toast-close-button:hover,
.toast-close-button:focus {
  color: #000000;
  text-decoration: none;
  cursor: pointer;
  opacity: 0.4;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
  filter: alpha(opacity=40);
}
/*Additional properties for button version
 iOS requires the button element instead of an anchor tag.
 If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
}
.toast-top-center {
  top: 0;
  right: 0;
  width: 100%;
}
.toast-bottom-center {
  bottom: 0;
  right: 0;
  width: 100%;
}
.toast-top-full-width {
  top: 0;
  right: 0;
  width: 100%;
}
.toast-bottom-full-width {
  bottom: 0;
  right: 0;
  width: 100%;
}
.toast-top-left {
  top: 12px;
  left: 12px;
}
.toast-top-right {
  top: 12px;
  right: 12px;
}
.toast-bottom-right {
  right: 12px;
  bottom: 12px;
}
.toast-bottom-left {
  bottom: 12px;
  left: 12px;
}
#toast-container {
  position: fixed;
  z-index: 999999;
  /*overrides*/

}
#toast-container * {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
#toast-container > div {
  position: relative;
  overflow: hidden;
  margin: 0 0 6px;
  padding: .5rem;
  width: 300px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  border-radius: 3px 3px 3px 3px;
  background-position: 15px center;
  background-repeat: no-repeat;
  -moz-box-shadow: 0 0 12px #999999;
  -webkit-box-shadow: 0 0 12px #999999;
  box-shadow: 0 0 12px #999999;
  color: #ffffff;
  opacity: 0.95;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=95);
  filter: alpha(opacity=95);
}
#toast-container > :hover {
  -moz-box-shadow: 0 0 12px #000000;
  -webkit-box-shadow: 0 0 12px #000000;
  box-shadow: 0 0 12px #000000;
  opacity: 1;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  filter: alpha(opacity=100);
  cursor: pointer;
}

#toast-container > .toast-info {
  @extend .icon-info-light;
  @extend .color-combo-info;
  @include style_elements(true); //alt styles
}
#toast-container > .toast-error {
  @extend .icon-warning-dark;
  @extend .color-combo-error;
  @include style_elements(true); //alt styles
}
#toast-container > .toast-success {
  @extend .icon-check-circle-light;
  @extend .color-combo-success;
  @include style_elements(true); //alt styles
}
#toast-container > .toast-warning {
  @extend .icon-warning-light;
  @extend .color-combo-attention;
  @include style_elements(false); //std styles
}
#toast-container > .toast::after {
  clear:both;
}
#toast-container > .toast::before { //icon
  font-family: 'snap-icons';
  float:left;
  font-size: 2.25rem;
  margin-right: .5rem;
  line-height: 1.375;
}

#toast-container.toast-top-center > div,
#toast-container.toast-bottom-center > div {
  width: 300px;
  margin: auto;
}
#toast-container.toast-top-full-width > div,
#toast-container.toast-bottom-full-width > div {
  width: 96%;
  margin: auto;
}
.toast {
  @extend .color-combo-1;
}
.toast-success {
  @extend .color-combo-success;
}
.toast-error {
  @extend .color-combo-error;
}
.toast-info {
  @extend .color-combo-1;
}
.toast-warning {
  @extend .color-combo-attention;
}
.toast-progress {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  background-color: #000000;
  opacity: 0.4;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
  filter: alpha(opacity=40);
}
/*Responsive Design*/
@media all and (max-width: 240px) {
  #toast-container > div {
    padding: 8px 8px 8px 50px;
    width: 11em;
  }
  #toast-container .toast-close-button {
    right: -0.2em;
    top: -0.2em;
  }
}
@media all and (min-width: 241px) and (max-width: 480px) {
  #toast-container  > div {
    padding: 8px 8px 8px 50px;
    width: 18em;
  }
  #toast-container .toast-close-button {
    right: -0.2em;
    top: -0.2em;
  }
}
@media all and (min-width: 481px) and (max-width: 768px) {
  #toast-container > div {
    padding: 15px 15px 15px 50px;
    width: 25em;
  }
}

/*
 * AngularJS-Toaster
 * Version 0.3
*/
#toast-container > div.ng-enter,
#toast-container > div.ng-leave
{
  -webkit-transition: 250ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
  -moz-transition: 250ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
  -ms-transition: 250ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
  -o-transition: 250ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
  transition: 250ms cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
}

#toast-container > div.ng-enter.ng-enter-active,
#toast-container > div.ng-leave {
  opacity: 0.8;
}

#toast-container > div.ng-leave.ng-leave-active,
#toast-container > div.ng-enter {
  opacity: 0;
}

#toast-container {
  .toast-tap-to-dismiss .toast-title, .toast-tap-to-dismiss .toast-message:first-child {
    @extend .icon-close;
    &:before {
      float:right;
      font-size: 0.61rem;
      font-family: 'snap-icons';
    }
  }
  .toast-bumps {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  .toast-bumps-item {
    display: inline-block;
    float: right;
    margin-left: 0.25rem;
  }
  .toast-bumps-item, .toast-bump {
    a {
      @extend .font-size-xsmall;
      @extend .semibold;
      color: inherit;
    }
  }
  .toast-bumps-item button {
     @extend .font-size-xsmall;
    margin:0;
    margin-top: 0.61rem;
    padding: 0.125rem 0.305rem;
    background-color: transparent;
    border: 1px solid $body-bg-color;
  }
  .toast-warning .toast-bumps-item button {
    color: $body-font-color;
    border: 1px solid $body-font-color;
  }
}
