table.color-table {
  border: none;
  margin-bottom: 4rem;
  thead {
    border: 1px groove $color-5;
  }
  tr {
    border-left: 1px $color-5 solid;
    border-right: 1px $color-5 solid;
    &:last-child {
      border-bottom: 1px $color-5 solid;
    }
    &.divider {
      border-bottom: 4px solid $color-5;
    }
    &.break {
      border-left: none;
      border-right: none;
      border-bottom: none;
      border-top: 1px solid $color-5;
      height: 5rem;
    }
  }
  & thead>tr>th {
    font-family: open_sansbold, sans-serif;
    font-weight: normal;
    text-align: center;
  }
  & tbody>tr>td {
    text-align: center;
    padding: 5px;
    &:first-child {
      text-align: left;
    }
    &.outlined {
      border-top: 1px solid white;
    }
  }
  &.combo-table {
    & thead>tr>th, & tbody>tr>td {
      text-align: center;
    }
  }

  kbd {
    &:not([class*="color-combo-"]) {
      background-color: $color-gray-10;
      color: $body-text-color;
    }
    border: 0;
    &:not([class*="text-color-"] or [class*="color-combo-"]) {
      color: black;
    }
  }
}

[code-example-id] h2 {
  @extend .text-color-5;
}

// Used for grid.html in the QSG to modify the grid css when used in a code example.
.grid-style {
  margin-bottom: 1rem;
  .label {
    margin-bottom: 0;
  }
  div.column, div.columns {
    background-color: #eee;
    border: 1px silver dashed;
  }
}
// Add borders to the columns and rows for visual reference
.row.example {
  border: 1px solid black;
  .column.example, .columns.example {
    border: 1px solid green;
  }
  .row .column.example, .row .columns.example {
    border: 1px solid red;
    .row .column.example, .row .columns.example {
      border: 1px solid blue;
    }
  }
}

.icon-list-icon {
  display: inline-block;
  min-width: 2rem;
}

.grid-padding-example {
  @extend .font-size-small;
  img, p {
    //margin-top: 1rem;
    margin-bottom: 0;
  }
  & .no-padding img {
    @extend .no-margin;
    height: 149.109px;
    width: 100%;
  }
}