@import "global";
@import "grid";
@import "controls";
@import "forms";

//
// Snap Field Layout Variables
//
$include-html-field-layout-classes : $include-html-classes !default;
//$include-html-classes is defined in _global.scss

$field-layout-inline : false !default;

// This is used to set inline rules. If labelcols exists...
$field-layout-labelcols : 3 !default;

// Total Columns is 12 by default. Defined in _grid.scss
$field-layout-inputcols : ($total-columns - $field-layout-labelcols) !default;
$field-layout-label-align : left !default;

//
// @MIXINS
//

// Called to apply grid rules to input-sets. For inline layouts, labels and inputs are side-by-side. Input sets
// occupy the space of an input, but can contain multiple inputs (WITH individual labels, if you're careful!).
@mixin field-layout-input-set($inputcols) {
  @if $inputcols {
    &:not(.wrap-inputs), &:not(.wrap-inputs)>label.wrap-input, &.wrap-inputs>label {
      & > ul.input-set {
        @include grid-column($inputcols, $collapse: true);
      }
    }
  }
}

@mixin field-layout-radio-checkbox-set($inputcols) {
  @if $inputcols {
    // i.e. for inline layouts
    &:not(.wrap-inputs), &:not(.wrap-inputs) > label.wrap-input, &.wrap-inputs > label {
      & > ul.radio-set, & > ul.checkbox-set {
        @include grid-column($inputcols, $collapse: true);
      }
    }
  }
}

@mixin label-input-cols($labelcols: false) {
  $inputcols: false;
  @if $labelcols {
    $inputcols: ($total-columns - $labelcols);
  }

  %label-box-style-#{$labelcols} {
    clear: both;
    display: block;
    padding: strip-unit($label-spacing / 2) * $rem-base + $input-border-width 0.625rem strip-unit($label-spacing / 2) * $rem-base + $input-border-width 0;
    margin-bottom: $control-spacing;
    @include grid-column($labelcols, $collapse: null);
  }

  // You can put ".wrap-inputs" at the parent to affect all children, or put ".wrap-input" on the label itself.
  &:not(.wrap-inputs) {
    &>label, &>.label-area {
      &:not(.wrap-input), &.wrap-input>:first-child, &.wrap-input {
        @extend %label-box-style-#{$labelcols};
      }
    }
  }

  &.wrap-inputs {
    &>label>:first-child {
      @extend %label-box-style-#{$labelcols};
    }
  }

  %field-input-#{$inputcols} {
    @include grid-column($inputcols);
    padding-left: $control-spacing / 2;
  }
  &, &>label.wrap-input, &.wrap-inputs>label {

    & > input[type="radio"],
    & > input[type="checkbox"]
    {
      @include grid-column($collapse:null);
    }

    @each $value in $input-types-text {
      & > input[type="#{$value}"] {
        @extend %field-input-#{$inputcols};
      }
    }
    & > select,
    & > .input-area,
    & > textarea {
      @extend %field-input-#{$inputcols};
    }
    & > fieldset
    {
      @include grid-column($inputcols, $collapse:null);
    }
    & > select[multiple] {
      padding-top: 0;
      padding-bottom: 0;
      padding-right: 0;
    }
    &>.input-area {
      padding: 0;
      &.std-padding { padding: $control-spacing / 2;}
    }
  }
}



@mixin field-layout($base:false,$inline : $field-layout-inline, $labelcols : $field-layout-labelcols, $inputcols : $field-layout-inputcols, $label-align : false) {
  @if $labelcols {
    $inputcols : ($total-columns - $labelcols);
  }

  @if $inline {
    @include label-input-cols($labelcols);
    @include field-layout-input-set($inputcols);
    @include field-layout-radio-checkbox-set($inputcols);
  }

  @if $base {
    @include grid-row('collapse');
    @include field-layout-input-set(false);
    @include field-layout-radio-checkbox-set(false);
    @include grid-column($collapse:true, $float:false);

    &>.column, &>.columns {
      @include grid-column($collapse:true, $float:false);
      padding-right: $fieldset-padding;
    }
    &>[class*="column"]:last-child, &>[class*="column"].break {
      padding-right: 0;
    }
  }

  @if $label-align { //TODO Probably called too many times by the inline- loop
    &:not(.wrap-inputs) {
      &>label, &>.label-area {
        &:not(.wrap-input), &.wrap-input>:first-child {
          text-align: $label-align;
        }
      }
    }
    &.wrap-inputs {
      & > label > :first-child, & > .label-area > :first-child {
        text-align: $label-align;
      }
    }
  }
}

@include exports("field-layout") {
  @if $include-html-field-layout-classes {

    .label-area {
      @extend label;
      cursor: $cursor-default-value;
    }

    .field-layout {
      @include field-layout($base:true, $label-align: $field-layout-label-align);

      @for $cols from 1 through 11 {
        &.inline-#{$cols}, .inline-#{$cols} {
          @include field-layout($inline : true, $labelcols : $cols);
        }
      }
      &.labels-right, .labels-right {
        @include field-layout($inline : false, $labelcols : false, $label-align : right);
      }
      &.labels-left, .labels-left {
        @include field-layout($inline : false, $labelcols : false, $label-align : left);
      }

      //Hackish :>(
      //lone radios (not in a radio-set, ect) get input sized by default
      &, &>.column {
        &, &>label {
          >input[type="radio"],  >input[type="checkbox"] {
            margin-bottom: $radio-bottom-margin;
            &.compact {
              margin-bottom: $control-spacing / 2;
            }
          }
        }
      }
    }
  }

  @for $i from 0 through 12 {
    $height: ($i * ($control-row-height + $control-spacing));
    @if ($i != 0) {
      .height-#{$i} {
        height: $height !important;

        &.control-margin,
        &.radio-set:not(.input-sized),
        &.checkbox-set:not(.input-sized) {
          height: zero-if-negative($height - $control-spacing)  !important;
        }
      }
      fieldset.height-#{$i} { //couldn't figure out how to nest this
        height: zero-if-negative($height - $control-spacing)  !important;
      }
    }

    //Height when dealing with stacked layout.  $j is number of labels, $i is number of inputs
    @for $j from 0 through 12 {
      $stacked-height: $i * ($control-row-height + $control-spacing) + $j * $label-stacked-height;
      @if not ($i == 0 and $j == 0) {
        .stacked-height-#{$j}-#{$i} {
          height: $stacked-height !important;

          &.control-margin,
          &.radio-set:not(.input-sized),
          &.checkbox-set:not(.input-sized) {
            height: zero-if-negative($stacked-height - $control-spacing)  !important;
          }
        }
        fieldset.stacked-height-#{$j}-#{$i} { //couldn't figure out how to nest this
          height: zero-if-negative($stacked-height - $control-spacing)  !important;
        }
      }
    }

  }

  .control-margin {
    margin-top: 0 !important;
    margin-bottom: $control-spacing !important;
  }
}

