@import "global";
@import "grid";


/// tabs.globals {
$include-html-tabs-classes: $include-html-classes !default;

//Settings applied to the actual tabs
$tabs-navigation-border-bottom-color: $color-3 !default;
$tabs-navigation-border-bottom-width: 25px;
$tabs-navigation-vertical-border-right-width: 5px;
$tabs-navigation-padding-list: 0.5625rem 0.625rem !default;
$tabs-navigation-bg-color: $color-gray-20 !default;
$tabs-navigation-bg-color-hover: $color-gray-30 !default;
$tabs-navigation-text-color: $color-gray-70 !default;
$tabs-navigation-active-bg-color: $color-3 !default;
$tabs-navigation-active-bg-color-hover: $tabs-navigation-active-bg-color !default;
$tabs-navigation-active-text-color: $body-text-color-alt !default;
$tabs-navigation-font-size: rem-calc(16) !default;
$tabs-vertical-navigation-margin-bottom: 1.25rem !default;

//Settings applied to the tab's content
$tabs-content-margin-bottom: rem-calc(24) !default;
$tabs-content-padding: $column-gutter/2 !default;
/// } tabs.globals

@include exports("tab") {
  @if $include-html-tabs-classes {
    .tabs {
      @include clearfix;
      border-bottom: $tabs-navigation-border-bottom-width solid $tabs-navigation-border-bottom-color;
      margin-bottom: 0 !important;
      margin-left: 0;
      dd:first-child, li:first-child, .tab-title:first-child {
        margin-left:0;
      }
      dd, li, .tab-title {
        cursor: pointer;
        position: relative;
        margin-bottom: 0 !important;
        list-style-type: none;
        float: $default-float;
        background-color: $tabs-navigation-bg-color;
        color: $tabs-navigation-text-color;
        margin-left:3px;
        font-weight: 500;
        & > a, & > span, & > .tab-title-content {
          display: list-item;
          color: $tabs-navigation-text-color;
          text-decoration: none;
          padding: $tabs-navigation-padding-list;
        }
        & > a.icon {
          @extend a.icon
        }
        .tab-ctrl > a.icon {
          color: inherit;
        }
        .tab-ctrl > a.icon:hover, .tab-ctrl > a.icon:hover::before {
          color: $color-6;
          text-shadow: none;
        }
        a.tab-heading{
          color: inherit;
          text-decoration: inherit;
        }
        .tab-ctrl {
          float: right;
          padding-left: .625rem;
        }
        &:not(:hover, .active) .tab-ctrl a  {
          color: $color-gray-70;
        }
        padding: 0;
        font-size: $tabs-navigation-font-size;
        &:hover {
          background-color: $tabs-navigation-bg-color-hover;
        }
        &.active {
          background-color: $tabs-navigation-active-bg-color;
          color:$tabs-navigation-active-text-color;
          & > a, & > span, & > .tab-title-content {
            color:$tabs-navigation-active-text-color;
            text-decoration: none;
          }
          &:hover {
            background-color: $tabs-navigation-active-bg-color-hover;
          }
        }
      }
      li.tab-button {
        background-color: transparent;
        &:before {
          color: $anchor-icon-color;
        }
        &:hover, &:focus, &:active {
          background-color: $tabs-navigation-bg-color;
        }
        a.icon:hover, a.icon:hover::before {
          text-shadow: none;
        }
      }
      &.radius {
        dd:first-child, .tab:first-child {
          @include side-radius($default-float, $global-radius);
        }
        dd:last-child, .tab:last-child {
          @include side-radius($opposite-direction, $global-radius);
        }
      }
      &:not(.vertical){
        .tab-row{
          display: inline-block;
        }
      }
      &.vertical {
        border-bottom: 0;
        border-#{$opposite-direction}: $tabs-navigation-vertical-border-right-width solid $tabs-navigation-border-bottom-color;
        dd, li, .tab-title {
          position: inherit;
          float: none;
          display: block;
          top: auto;
          margin-left:inherit;
          margin-top:3px;
        }
        dd:first-child, li:first-child, .tab-title:first-child {
          margin-top:0;
        }
      }
    }

    .tabs-content {
      @include clearfix;
      margin-bottom: $tabs-content-margin-bottom;
      width: 100%;
      overflow: auto;
      > .content {
        display: none;
        float: $default-float;
        padding: $tabs-content-padding 0;
        width: 100%;
        &.active { display: block; float: none; }
        &.contained { padding: $tabs-content-padding; }
      }
      &.vertical {
        border-#{$default-float}: $tabs-navigation-vertical-border-right-width solid $tabs-navigation-border-bottom-color;
        display: block;
        > .content { padding: 0 $tabs-content-padding; }
      }
    }
    @media #{$medium-up} {
      .tabs {
        &.vertical {
          width: 20%;
          max-width: 20%;
          float: $default-float;
          margin: 0 0 $tabs-vertical-navigation-margin-bottom;
        }
      }
      .tabs-content {
        &.vertical {
          width: 80%;
          max-width: 80%;
          float: $default-float;
          margin-#{$default-float}: -$tabs-navigation-vertical-border-right-width;
          padding-#{$default-float}: 1rem;
        }
      }
    }
    .no-js {
      .tabs-content > .content {
        display: block;
        float: none;
      }
    }
  }
}
