@import "global";

//
// @variables
//
$include-html-media-classes: $include-html-classes !default;

/// progress-bar.globals {
// We use this to set the progress bar height
$progress-bar-height: rem-calc(25) !default;
$progress-bar-color: $color-1 !default;

// We use these to control the border styles
$progress-bar-border-color: $color-3 !default;
$progress-bar-border-size: 1px !default;
$progress-bar-border-style: solid !default;
/// } progress-bar.globals
$progress-bar-border-radius: $global-radius !default;
/// progress-bar.globals {
// We use these to control the margin & padding
$progress-bar-pad: rem-calc(2) !default;
$progress-bar-margin-bottom: rem-calc(10) !default;

// We use these to set the meter colors
$progress-meter-color: $primary-color !default;
$progress-meter-secondary-color: $secondary-color !default;
$progress-meter-success-color: $success-color !default;
$progress-meter-alert-color: $color-alert !default;
$progress-meter-attention-color: $color-attention !default;
$progress-meter-info-color: $color-info !default;
/// } progress-bar.globals
// @mixins
//
// We use this to set up the progress bar container
@mixin progress-container {
  background-color: $progress-bar-color;
  height: $progress-bar-height;
  border: $progress-bar-border-size $progress-bar-border-style $progress-bar-border-color;
  padding: $progress-bar-pad;
  margin-bottom: $progress-bar-margin-bottom;
}

// @mixins
//
// $bg - Default: $progress-meter-color || $primary-color
@mixin progress-meter($bg:$progress-meter-color) {
  background: $bg;
  height: 100%;
  display: block;
}

@include exports("progress-bar") {
  @if $include-html-media-classes {

    /* Progress Bar */
    .progress {
      @include progress-container;

      // Meter
      .meter {
        @include progress-meter;
        @for $size from 0 through 100 {
          &.width-#{$size} {
            width : #{$size * 1%};
          }
        }
      }
      &.secondary .meter { @include progress-meter($bg:$progress-meter-secondary-color); }
      &.success .meter { @include progress-meter($bg:$progress-meter-success-color); }
      &.alert .meter { @include progress-meter($bg:$progress-meter-alert-color); }
      &.attention .meter { @include progress-meter($bg:$progress-meter-attention-color); }
      &.info .meter { @include progress-meter($bg:$progress-meter-info-color); }
      &.color-1 .meter { @include progress-meter($bg:$color-1); }
      &.color-2 .meter { @include progress-meter($bg:$color-2); }
      &.color-3 .meter { @include progress-meter($bg:$color-3); }
      &.color-4 .meter { @include progress-meter($bg:$color-4); } //same as $primary
      &.color-5 .meter { @include progress-meter($bg:$color-5); }
      &.color-6 .meter { @include progress-meter($bg:$color-6); }

      &.radius { @include radius($progress-bar-border-radius);
        .meter { @include radius($progress-bar-border-radius - 1); }
      }

      &.round { @include radius(1000px);
        .meter { @include radius(999px); }
      }
    }
  }
}
