//==========================================================================
//   GLOBAL Element and Attribute Styles
//==========================================================================

@import
  "functions",
  "components/global"
;

@mixin snap-anchor($default: true, $decoration : $anchor-text-decoration){
  @if($default) {
    outline: none;
    color: $anchor-text-color;
    &:hover {
      color : $anchor-text-color-hover;
    }
  }
  @if($decoration) {
    text-decoration: $decoration;
  }
}

// Anchors
// All anchors (in any Snap project, with focus or not) get 1) the default anchor color and 2) lighten on hover.
// TODO This should be ALL managed in this file. Some is managed in panels, masthead, etc. Sames is true in footer section
// at the bottom of this file (line 480 or so).

@mixin snap-anchor-icon($color:$anchor-icon-color, $hover: $anchor-icon-color-hover, $stroke: $color-1, $blur: $color-black) {
  color: $color;
  text-decoration: $anchor-icon-decoration;

  //The if was problematic for things like the masthead toggle, the hover rule for the
  //  non mimi-minimized was strong enough to over rule the minimized one.
  //@if($anchor-icon-decoration != $anchor-icon-decoration-hover or $color != $hover){
    &:hover, &:focus {
      color: $hover;
      text-decoration: $anchor-icon-decoration-hover;
    }
  //}

  @if ($stroke != null) {
    &:hover, &:focus {
      &::before {
        @include icon-shadow($stroke, $blur);
      }
    }
  }

  &.button::before {
    text-decoration: none !important;
    text-shadow: none !important;
  }

  // .unavailable is used by breadcrumbs/paging
  &.disabled, &.disabled-icon, .unavailable &, .disabled & {
    opacity: 0.5 !important;
    cursor: not-allowed !important;
    &::before {
      text-shadow: none !important;
    }
  }
}

@mixin icon-shadow($stroke: $color-1, $blur: $color-black) {
  text-shadow: -1px -1px 0 $stroke,
  1px -1px 0 $stroke,
  -1px 1px 0 $stroke,
  1px 1px 0 $stroke,
  2px 2px 1px $blur,
}

@mixin snap-hr {
  border: 1px solid $color-gray-30;
}

@include exports("snap_styles") {

  @if ($include-compatibility-icons) {
    .is-icon {
      @extend a.icon;
    }
  }

  a {
    @include snap-anchor;
    &.action-icon, &.button-link, &.image-link, &.close-reveal-modal {
      @include snap-anchor($default: false, $decoration: none);
    }
  }

  // html
  // Always display a vertical scroll bar. Keeps the menu bar from jumping left and right based on page-length.
  html { overflow-y : scroll; }


  a.icon {
    @include snap-anchor-icon();
  }


  // All inputs, selects and textareas should have white background, gray-60 border on hover (unless they're disabled.

  hr {
    @include snap-hr;
    &.color-1 { border: 1px solid $color-1             ; }
    &.color-1-light { border: 1px solid $color-1-light ; }
    &.color-1-dark  { border: 1px solid $color-1-dark  ; }
    &.color-2 { border: 1px solid $color-2             ; }
    &.color-2-light { border: 1px solid $color-2-light ; }
    &.color-2-dark  { border: 1px solid $color-2-dark  ; }
    &.color-3 { border: 1px solid $color-3             ; }
    &.color-3-light { border: 1px solid $color-3-light ; }
    &.color-3-dark  { border: 1px solid $color-3-dark  ; }
    &.color-4 { border: 1px solid $color-4             ; }
    &.color-4-light { border: 1px solid $color-4-light ; }
    &.color-4-dark  { border: 1px solid $color-4-dark  ; }
    &.color-5 { border: 1px solid $color-5             ; }
    &.color-5-light { border: 1px solid $color-5-light ; }
    &.color-5-dark  { border: 1px solid $color-5-dark  ; }
    &.color-6 { border: 1px solid $color-6             ; }
    &.color-6-light { border: 1px solid $color-6-light ; }
    &.color-6-dark  { border: 1px solid $color-6-dark  ; }
  }

  //This selector eliminates the standard select 'pip' in IE10. Looks like Chrome and Firefox now.

  // [placeholder]
  // This section makes placeholder text light gray in all browsers (BUT not IE9).
  [placeholder] {
    &::-webkit-input-placeholder {
      color : $color-info;
    }

    &:-moz-placeholder { /* Firefox 18- */
      color : $color-info;
    }

    &::-moz-placeholder { /* Firefox 19+ */
      color : $color-info;
    }

    &:-ms-input-placeholder {
      color: $color-info;
    }
  }

  // Miscellaneous element and attribute styles

  // All angular clickable elements will now display the hand (pointer) cursor.
  @if $include-angular-classes {
    *[ng-click] {
      cursor: pointer;
    }
  }
  *[onclick] {
    cursor: pointer;
  }

  //==========================================================================
  //   $ GLYPH SIZING
  //==========================================================================

  ///**
  // * Comment: Generate .glyph-25 through .glyph-1000 to size icons (glyph-350 = font-size: 3.5rem, for example)
  //
  //    NOTE: These icons will not work locally in Firefox - only on the server. They will work locally in IE, Safari & Chrome.

  .glyph-tiny:before     { font-size: $glyph-tiny !important; }
  .glyph-xsmall:before   { font-size: $glyph-xsmall !important; }
  .glyph-small:before    { font-size: $glyph-small  !important; }
  .glyph-medium:before   { font-size: $glyph-medium  !important; }
  .glyph-large:before    { font-size: $glyph-large  !important; }
  .glyph-xlarge:before   { font-size: $glyph-xlarge !important; }
  .glyph-jumbo:before    { font-size: $glyph-jumbo !important; }

  @if $include-compatibility-glyphsize {
    @for $size from 1 through 40 {
      .glyph-#{$size*25}:before {
        font-size : #{$size*.25}rem;
      }
    }
  }

  //==========================================================================
  //    SNAP CLASSES
  //==========================================================================

  //  Font Sizes
  .font-size-tiny   { font-size: $font-size-tiny !important; }
  .font-size-xsmall { font-size: $font-size-xsmall !important; }
  .font-size-small  { font-size: $font-size-small  !important; }
  .font-size-medium { font-size: $font-size-medium  !important; }
  .font-size-large  { font-size: $font-size-large  !important; }
  .font-size-xlarge { font-size: $font-size-xlarge !important; }
  .font-size-jumbo  { font-size: $font-size-jumbo !important; }

  @if $include-compatibility-fontsize {
    .font-size-50 , .font-size-50 >thead>tr>th, .font-size-50  >tbody>tr>td, .font-size-50 >tfoot>tr>td    { font-size: $font-size-50      !important; }
    .font-size-60 , .font-size-60 >thead>tr>th, .font-size-60 >tbody>tr>td, .font-size-60 >tfoot>tr>td    { font-size: $font-size-60      !important; }
    .font-size-70 , .font-size-70 >thead>tr>th, .font-size-70 >tbody>tr>td, .font-size-70 >tfoot>tr>td    { font-size: $font-size-70      !important; }
    .font-size-80 , .font-size-80 >thead>tr>th, .font-size-80 >tbody>tr>td, .font-size-80 >tfoot>tr>td    { font-size: $font-size-80      !important; }
    .font-size-90 , .font-size-90 >thead>tr>th, .font-size-90 >tbody>tr>td, .font-size-90 >tfoot>tr>td    { font-size: $font-size-90      !important; }
    .font-size-100, .font-size-100>thead>tr>th, .font-size-100>tbody>tr>td, .font-size-100>tfoot>tr>td    { font-size: $font-size-100     !important; }
    .font-size-110, .font-size-110>thead>tr>th, .font-size-110>tbody>tr>td, .font-size-110>tfoot>tr>td    { font-size: $font-size-110     !important; }
    .font-size-120, .font-size-120>thead>tr>th, .font-size-120>tbody>tr>td, .font-size-120>tfoot>tr>td    { font-size: $font-size-120     !important; }
    .font-size-130, .font-size-130>thead>tr>th, .font-size-130>tbody>tr>td, .font-size-130>tfoot>tr>td    { font-size: $font-size-130     !important; }
    .font-size-140, .font-size-140>thead>tr>th, .font-size-140>tbody>tr>td, .font-size-140>tfoot>tr>td    { font-size: $font-size-140     !important; }
    .font-size-150, .font-size-150>thead>tr>th, .font-size-150>tbody>tr>td, .font-size-150>tfoot>tr>td    { font-size: $font-size-150     !important; }
    .font-size-160, .font-size-160>thead>tr>th, .font-size-160>tbody>tr>td, .font-size-160>tfoot>tr>td    { font-size: $font-size-160     !important; }
    .font-size-170, .font-size-170>thead>tr>th, .font-size-170>tbody>tr>td, .font-size-170>tfoot>tr>td    { font-size: $font-size-170     !important; }
    .font-size-180, .font-size-180>thead>tr>th, .font-size-180>tbody>tr>td, .font-size-180>tfoot>tr>td    { font-size: $font-size-180     !important; }
    .font-size-190, .font-size-190>thead>tr>th, .font-size-190>tbody>tr>td, .font-size-190>tfoot>tr>td    { font-size: $font-size-190     !important; }
    .font-size-200, .font-size-200>thead>tr>th, .font-size-200>tbody>tr>td, .font-size-200>tfoot>tr>td    { font-size: $font-size-200     !important; }
  }

  .font-size-h1       { font-size: $font-size-h1; margin-top: rem-calc(44); }
  .font-size-h2       { font-size: $font-size-h2; margin-top: rem-calc(37); }
