  // Fonts
  // IMPORTANT, KEEP all these font classes in order so they cascade rules correctly

  // Define the base font class
  .regular {
    font-family: $font-family-sans-serif;
    font-weight: 400;
    font-style: normal;
  }

  // Define the other family classes
