//Typical TH styles are used in the left-most (not top) row.
.side-head-table {
  width: 100%;
  //@extend .no-hover;
  td, td kbd {
    text-align: center;
  }
  td:first-child {
    text-align: right;
    width: 20%;
    color: $table-head-font-color;
    background-color: $table-head-bg;
    @extend .semibold;
  }
  .center-list {
    li {
      display: inline-block;
      float: none;
    }
  }
}

// Necessary to make the masthead example play nice with the code-example directive
.masthead-positional {
  .masthead-container .masthead {
    position: absolute;
  }
  .content-inner {
    position: relative;
  }
}
.top-bar-fixed-highlighter:hover {
  .fixed {
    box-shadow: 0 0 4px 2px $color-error;
    &:hover {
      box-shadow: 0 0 0 0 transparent;
    }
  }
}

// Used in button.html
.width-20pct {
  width: 20%;
}

.html-tag {
  @include keystroke;
  @include radius($keystroke-radius);
  text-transform: uppercase;
}

$qsg-sidenav-width: 150;

.side-nav-fixed {
  position: fixed;
  margin-top: 0px;
  width: rem-calc($qsg-sidenav-width);
}

.side-nav-content {
  min-height: rem-calc(350);
  margin-left: rem-calc($qsg-sidenav-width + 30);
}
.side-nav-deprecated, .side-nav-deprecated:hover, li.active>a.side-nav-deprecated:hover {
  text-decoration: line-through;
}

.deprecated {
  background: url('../images/deprecated.png') no-repeat;
  transform: rotate(355deg) scale(.7);
  height: 6rem;
  position: absolute;
  z-index: 0;
  top: -14px;
  left: 100px;
  width: 655px;
  opacity: .7;
}

.html-attr, .class-attr {
  @include keystroke;
  @include radius($keystroke-radius);
}

.filename, .file-name {
  @include keystroke;
  @include radius($keystroke-radius);
}

.url-style, .book-title {
  @extend .italic;
}

/// reveal.mixin.01 {
.custom-reveal-class {
  @include reveal-modal-style(
          $bg: $color-gray-80,
          $text-color: $color-1,
          $padding: 2rem,
          $border: true,
          $box-shadow: true,
          $radius: false,
          $top-offset: $reveal-position-top
  );
  & h1, h2, h3, h4, h5, h6 {
    color: $color-white;
  }
}
/// } reveal.mixin.01

// These two are used for an illustration in icontip.html
.help-tip {
  @include icontip('This message will inhabit the pop-up!', $icon-class: 'icon-question-dark');
}

.info-tip {
  @include icontip($icon-class: 'icon-info-light');
}

.help-tip-with-base {
  @include icontip-base();
  @include icontip('No icontip class needed!', $icon-class: 'icon-question-dark');
}

//file: qsg.scss
//Call this mixin to create your own classes
.embedded-help-tip {
  @include icontip-base();
  @include icontip('This message will inhabit the pop-up!', $icon-class: 'icon-question-dark');
  @include embedded-icontip();
}

.tip-input {
  @include embedded-target();
}

//When multiple error classes are on the same input, last one (CSS/SCSS file order) wins.
// uses @mixin errortip($error-class, $message: null, $error-holder:'err-msg', $icons:$icons, $icon-class: null, $icon-char: null, $icon-font: 'snap-icons')
@include errortip(error-class-1, 'Error 1', $icon-class: 'icon-question-dark');
@include errortip(error-class-2, 'Error 2'); //defaults to $icon-class: icon-warning-dark
@include errortip(error-class-3, 'Error 3', $icon-class: 'icon-truck');
@include errortip(error-class-4, $icon-class: 'icon-barcode'); //No error text set, will only have a tooltip if attribute is used.
