// This file, snap_globals, is imported at the top of the global component.
// Therefore, it's available to all components (because they all import global.scss),
// and the values set here will be set first.
//

@import "functions";

//This file should only contain variables that we have defined ourselves, i.e. $colorXXX, etc..

// We use this to control whether or not CSS classes come through in generated CSS.
$include-html-classes: true !default; /* don't include in template */
$include-html-global-classes: $include-html-classes !default; /* don't include in template */
$include-html-abide-classes: false !default;

//Include classes designed to work with Angular JS 1.x
$include-angular-classes: true !default;

/* COMPATIBILITY FLAGS
 * $include-compatibility-<feature>
 *
 * This is how we deprecate Snap features without breaking them.
 *
 * All these features below are turned off by default.
 * To turn any of them on, set it's flag to TRUE in
 * your project's scss file.
*/

/* Include obsolete flags for stuff on its way out
 * Deprecated font size classes:
 *  font-size-50 (.5rem)
 *  font-size-60 (.6rem)
 *  font-size-70 (.7rem)
 * ...
 *  font-size-200 (2rem)
 * can be turned back on with $include-compatibility-fontsize: true
  */
$include-compatibility-fontsize: false !default;

/* Deprecated glyph size classes:
 *  glyph_size_025 (.25rem)
 *  glyph_size_050 (.5rem)
 *  glyph_size_075 (.75rem)
 * ...
 *  glyph_size_1000 (10rem)
 * can be turned back on with $include-compatibility-glyphsize: true
*/
$include-compatibility-glyphsize: false !default;

/// button.compat {
/* When true, button elements can be used without applying a button class.
 * When false, button elements require a button class (and fewer hover rules are generated).
 */
$include-compatibility-button: true !default;
///} button.compat

// See previous comment. Same applies here, i.e. you must add .icon AND .icon-whatever. Saves hovers.
$include-compatibility-icons: false !default; //check _icons.scss for override

//See comments above. No icon-bar component, unless this is set to TRUE in your project's SCSS file.
$include-compatibility-icon-bar: false !default

$service: $army !default;

$color-1: map-get($service, 'c1');
$color-2: map-get($service, 'c2');
$color-3: map-get($service, 'c3');
$color-4: map-get($service, 'c4');
$color-5: map-get($service, 'c5');
$color-6: map-get($service, 'c6');

// Numbered Color Variants
$color-1-light: scale_color($color-1, $lightness: 25%);
$color-2-light: scale_color($color-2, $lightness: 25%);
$color-3-light: scale_color($color-3, $lightness: 25%);
$color-4-light: scale_color($color-4, $lightness: 10%);
$color-5-light: scale_color($color-5, $lightness: 25%);
$color-6-light: scale_color($color-6, $lightness: 25%);

$color-1-dark: scale_color($color-1, $lightness: -15%);
$color-2-dark: scale_color($color-2, $lightness: -15%);
$color-3-dark: scale_color($color-3, $lightness: -25%);
$color-4-dark: scale_color($color-4, $lightness: -25%);
$color-5-dark: scale_color($color-5, $lightness: -25%);
$color-6-dark: scale_color($color-6, $lightness: -25%);

$backdrop-dark: $color-5-dark;
$backdrop-dark-transparent: rgba($backdrop-dark, .8);

// Utility Colors
$color-error:     #B32321; //error, required and alert are all the same color, just for convenience
$color-required:  #B32321;
$color-alert:     #B32321;
$color-attention: #ffe083;
$color-success:   #47813e;
$color-info:      #aaaaaa;
$color-white:     #ffffff;
$color-black:     #000000;

// Utility Color hovers
$color-error-hover:     scale_color($color-error, $lightness: 20%);
$color-required-hover:  scale_color($color-required, $lightness: 15%);
$color-attention-hover: scale_color($color-attention, $lightness: -12%);
$color-success-hover:   scale_color($color-success, $lightness: 15%);
$color-info-hover:      scale_color($color-info, $lightness: 40%);
$color-white-hover:     scale_color($color-white, $lightness: -15%);
$color-black-hover:     scale_color($color-black, $lightness: 15%);

// Grayscale colors
$color-gray-10:   scale_color($color-white, $lightness: -2%);  // #fafafa
$color-gray-20:   scale_color($color-white, $lightness: -7%);  // #ededed
$color-gray-30:   scale_color($color-white, $lightness: -12%); // #e0e0e0
$color-gray-40:   scale_color($color-white, $lightness: -20%); // #cccccc
$color-gray-50:   scale_color($color-white, $lightness: -25%); // #bfbfbf
$color-gray-60:   scale_color($color-white, $lightness: -30%); // #b3b3b3
$color-gray-70:   scale_color($color-white, $lightness: -40%); // #999999
$color-gray-80:   scale_color($color-white, $lightness: -50%); // #808080
$color-gray-90:   scale_color($color-white, $lightness: -65%); // #595959

//TODO Some of these colors are being used, but should be mapped to Snap colors instead.
//They are also defined in _globals.scss. So deal with those also.
$white       : #FFFFFF;
$ghost       : #FAFAFA;
$snow        : #F9F9F9;
$vapor       : #F6F6F6;
$white-smoke : #F5F5F5;
$silver      : #EFEFEF;
$smoke       : #EEEEEE;
$gainsboro   : #DDDDDD;
$iron        : #CCCCCC;
$base        : #AAAAAA;
$aluminum    : #999999;
$jumbo       : #888888;
$monsoon     : #777777;
$steel       : #666666;
$charcoal    : #555555;
$tuatara     : #444444;
$oil         : #333333;
$jet         : #222222;
$black       : #000000;


// Used by many foundation components
$primary-color:   $color-4;
$secondary-color: $color-2;

//TODO These should be removed
$alert-color:     $color-error;
$success-color:   $color-success;
$warning-color:   $color-attention;
$info-color:      $color-info;

// We use these to define default font stacks
$font-family-sans-serif:            "Open Sans",            sans-serif  ;
$font-family-sans-serif-condensed:  "Open Sans Condensed",  sans-serif  ;
$font-family-serif:                 "Georgia",              serif       ;
$font-family-monospace:             "Consolas",             monospace   ;

// We use these to control various global styles
$body-bg-color: $color-white;
$body-text-color: $color-5;
$body-text-color-disabled: $color-gray-60;
$body-text-color-alt: $color-1;

// Snap elements have square corners
$global-radius: 0px;

// We use these to control header font styles
$header-text-color: $color-5;
$subheader-text-color: $color-3;
$header-line-height: 1.4;
$header-top-margin: .2rem;
$header-bottom-margin: .5rem;

$glyph-tiny:    0.75rem;
$glyph-xsmall:     1rem;
$glyph-small:   1.25rem;
$glyph-medium:  1.50rem;
$glyph-large:      2rem;
$glyph-xlarge:  2.50rem;
$glyph-jumbo:      4rem;

// Font sizes
// DO NOT USE - THESE ARE DEPRICATED IN FAVOR OF THE SEMANTIC SIZE CLASSES
$font-size-50:       .5rem;
$font-size-60:       .6rem;
$font-size-70:       .7rem;
$font-size-80:       .8rem;
$font-size-90:       .9rem;
$font-size-100:       1rem;
$font-size-110:     1.1rem;
$font-size-120:     1.2rem;
$font-size-130:     1.3rem;
$font-size-140:     1.4rem;
$font-size-150:     1.5rem;
$font-size-160:     1.6rem;
$font-size-170:     1.7rem;
$font-size-180:     1.8rem;
$font-size-190:     1.9rem;
$font-size-200:       2rem;

$font-size-tiny:     .6rem;
$font-size-xsmall:   .7rem;
$font-size-small:    .8rem;
$font-size-medium:    1rem;
$font-size-large:   1.5rem;
$font-size-xlarge:  2.5rem;
$font-size-jumbo:   3.5rem;

// We use these to control header font sizes
$font-size-h1: rem-calc(44);
$font-size-h2: rem-calc(37);
$font-size-h3: rem-calc(27);
$font-size-h4: rem-calc(23);
$font-size-h5: rem-calc(18);
$font-size-h6: 1rem;

// We use these to style <code> tags
//$code-color: $color-5-light;

// We use these to style anchors
$anchor-text-decoration: none;
$anchor-text-decoration-hover: underline;
$anchor-text-color: $color-6;
$anchor-text-color-alt: $color-1-light;
$anchor-text-color-hover: $color-6;
$anchor-text-color-hover-alt: $color-gray-20;
$anchor-icon-color: $color-6;
$anchor-icon-color-hover: $anchor-icon-color;
$anchor-icon-color-alt: $color-1;
$anchor-icon-color-hover-alt: $color-1-light;
$anchor-icon-decoration: none;
$anchor-icon-decoration-hover: $anchor-icon-decoration;
$anchor-icon-hover-stroke-alt: $color-gray-70;
$anchor-icon-hover-blur-alt: $color-gray-80;

// Icon colors
$icon-color: $color-4; //not an anchor, styling for icons that are used as anchors is $anchor-icon-color, above.
$icon-color-hover: $icon-color;
$icon-color-alt: $color-2;
$icon-color-alt-hover: $icon-color-alt;

// Global hover transition time
$global-hover-transition-time: 0ms;

// Used to style the close icon on popups and other dialogs
$close-text-color: $color-gray-60;
$close-text-color-alt: $color-5-light;
$close-text-color-hover: $close-text-color;
$close-text-color-hover-alt: $close-text-color-alt;

// Used to style the shadows
$shadow-color: $color-gray-60;
$shadow-color-alt: $color-gray-90;

// We use these to style the <hr> element
$hr-border-color: $color-gray-30;
$hr-margin: rem-calc(20);

$row-width: rem-calc(1280) !default;

// Media Query Ranges
$small-range: (0em, 40em);
$medium-range: (40.063em, 64em);
$large-range: (64.063em, 90em);
$xlarge-range: (90.063em, 120em);
$xxlarge-range: (120.063em, 99999999em);

// Types of inputs
$input-types-text: "text"
"password"
"date"
"datetime"
"datetime-local"
"month"
"week"
"email"
"number"
"search"
"tel"
"time"
"url"
"color";

$input-types-push: "radio"
"checkbox";

//============================================================================================
$label-text-color: $body-text-color;

$fieldset-margin : 0 0 18px 0;


// Panels - NOT THE PANEL COMPONENT - This is a global call-out for panel-like areas - Like in the Panel component
$panel-bg-color: $color-1-light;
$panel-bg-color-alt: $color-4;
$panel-border-color: $color-5-light;
$panel-callout-anchor-text-decoration: underline;

// Action attributes - buttons, sliders
$action-bg-color: $color-4;
$action-bg-color-hover: $color-4-light;
$action-bg-color-alt: $color-2;
$action-bg-color-alt-hover: $color-2-light;

// $include-html-panel-classes: $include-html-classes;
$action-bg-color: $color-4;
