$icons: () !default;
$icons-font-list: () !default;
@if if(not map-get($icons-font-list, "qsg-icons"), true, not map-get(map-get($icons-font-list, "qsg-icons"), "fonts-set")) {
  $icons-font-list: map-merge(
    $icons-font-list,
    (
      "qsg-icons": map-merge(
        (
            iconPrefix:"icon-qsg-",
            fonts-set: true,
            fontFamily:"qsg-icons",
            fontDir:"../qsg-icons/",
            "woff":"fonts/qsg-icons.woff?-jbkb7w8K49WiSWTU7Bt1JQ"
        ),
        if(map-get($icons-font-list, "qsg-icons"), map-get($icons-font-list, "qsg-icons"), ())
      ),
    )
  ) !global;
}

@if not map-get(map-get($icons-font-list, "qsg-icons"), "icons-included") {
  $icon-font-list: map-merge($icons-font-list, ("qsg-icons": map-merge(map-get($icons-font-list, "qsg-icons"), ("icons-included":true)))) !global;
  $icons: map-merge($icons, (
    "icon-qsg-ship":(code: "\00e900", fontFamily: "qsg-icons", iconPrefix: "icon-qsg-", class: "icon-qsg-ship"),
    "icon-qsg-microwave-oven":(code: "\00e901", fontFamily: "qsg-icons", iconPrefix: "icon-qsg-", class: "icon-qsg-microwave-oven"),
    "icon-qsg-caret-left-circle":(code: "\00ea40", fontFamily: "qsg-icons", iconPrefix: "icon-qsg-", class: "icon-qsg-caret-left-circle"),
    "icon-qsg-caret-up-circle":(code: "\00ea41", fontFamily: "qsg-icons", iconPrefix: "icon-qsg-", class: "icon-qsg-caret-up-circle"),
    "icon-qsg-caret-right-circle":(code: "\00ea42", fontFamily: "qsg-icons", iconPrefix: "icon-qsg-", class: "icon-qsg-caret-right-circle"),
    "icon-qsg-caret-down-circle":(code: "\00ea43", fontFamily: "qsg-icons", iconPrefix: "icon-qsg-", class: "icon-qsg-caret-down-circle")
  )) !global;
}