@import "global";

//
// @name _tables.scss
// @dependencies _global.scss
//

//
// @variables
//

/// table.globals {
$include-html-table-classes: $include-html-classes !default;

$table-width: null !default;
$table-font-size: $font-size-small;
$table-font-size-compact: $font-size-xsmall;

// These control the background color for the table and even rows (also see striped class)
$table-bg: $color-white !default;
$table-even-row-bg: $color-1-light !default;

// These control the table cell border style (also see no-border class)
$table-border-style: solid !default;
$table-border-size: 1px !default;
$table-border-color: $color-gray-30 !default;

// These control the table head styles
$table-head-bg: $color-3 !default;
$table-head-bg-alt: $color-white !default;
$table-head-bg-alert: $color-6 !default;
$table-head-font-size: inherit !default;
$table-head-font-size-compact: inherit !default;
$table-head-font-color: $body-text-color-alt !default;
$table-head-font-color-alt: $body-text-color !default;
$table-head-padding: rem-calc(8 10 10) !default;
$table-head-border-alt: 2px solid $color-3;

// These control the table foot styles
$table-foot-bg: $color-2 !default;
$table-foot-bg-alt: $color-white !default;
$table-foot-font-size: inherit !default;
$table-foot-font-size-compact: inherit !default;
$table-foot-font-color: $body-text-color !default;
$table-foot-padding: $table-head-padding !default;
$table-foot-border-alt: 2px solid $color-2;

// These control the row padding and font styles
$table-row-padding: rem-calc(9 10) !default;
$table-row-padding-compact: rem-calc(4 5) !default;
$table-row-font-size: inherit !default;
$table-row-font-size-compact: inherit !default;
$table-row-font-color: $body-text-color !default;
$table-line-height: 1.1 !default;

// These control the caption
$table-caption-bg: transparent !default;
$table-caption-font-color: $table-row-font-color !default;
$table-caption-font-size: inherit !default;
$table-caption-font-size-compact: inherit !default;

// These are for controlling the layout, display and margin of tables
$table-layout: auto !default;
$table-display: table-cell !default;
$table-margin-bottom: rem-calc(20) !default;

// Set the row-wise hover color (also see no-hover class)
$table-hover-bg-color: $color-2-light !default;
///} table.globals

//
// @mixins
//

@mixin table {
  background: $table-bg;
  margin-bottom: $table-margin-bottom;
  border: $table-border-style $table-border-size $table-border-color;
  table-layout: $table-layout;
  font-size: $table-font-size;
  & input[type='checkbox'] {
    margin-bottom: 0;
  }

  > caption {
    @extend .italic;
    background: $table-caption-bg;
    color: $table-caption-font-color;
    caption-side: bottom;
    text-align: right;
    font-size: $table-caption-font-size;
  }

  > thead {
    background: $table-head-bg;
    @extend .semibold;
    text-align: left;
    font-size: $table-head-font-size;
    color: $table-head-font-color;
    > tr > th, > tr > td {
      padding: $table-head-padding;
    }
  }

  > tfoot {
    background: $table-foot-bg;
    @extend .semibold;
    font-size: $table-foot-font-size;
    color: $table-foot-font-color;
    > tr > th, > tr > td {
      padding: $table-foot-padding;
    }
  }

  > tbody {
    vertical-align: top;
    font-size: $table-row-font-size;
    color: $table-row-font-color;
    text-align: $default-float;
    > tr {
      border-bottom: 1px solid $table-border-color;
      &:hover {
        background-color: $table-hover-bg-color;
      }
      > th, > td {
        padding: $table-row-padding;
      }
    }
  }

  > thead > tr > th,
  > tfoot > tr > th,
  > tfoot > tr > td,
  > tbody > tr > th,
  > tbody > tr > td,
  > tr > td {
    display: $table-display;
    line-height: $table-line-height;
  }
}

@mixin alternate() {
  > thead {
    background-color: $table-head-bg-alt;
    border-bottom: $table-head-border-alt;
    color: $table-head-font-color-alt;
  }
  > tfoot {
    background-color: $table-foot-bg-alt;
    border-top: $table-foot-border-alt;
  }
}

@mixin compact() {
  font-size: $table-font-size-compact;
  > thead, > tfoot, > tbody {
    font-size: $table-row-font-size-compact;
    > tr > td, > tr > th {
      padding: $table-row-padding-compact;
    }
  }
}

@mixin table-font-size($text-size: $table-row-font-size) {
  @if $text-size {
    & > * > tr > td, & > * > tr > th {
      font-size: inherit;
    }
  }
}

@include exports("table") {
  @if $include-html-table-classes {
    table.table {
      @include table();
    }
    .fixed-layout, .equal-width {
      table-layout: fixed !important;
    }

    // Vertical alignment classes
    thead, tbody, tfoot, td, th, tr {
      &.middle {
        vertical-align: middle !important;
      }
      &.top {
        vertical-align: top !important;
      }
      &.bottom {
        vertical-align: bottom !important;
      }
      &.baseline {
        vertical-align: baseline !important;
      }
    }
    table.no-border {
      border: none !important
    }
    table.full-width {
      width: 100%;
    }
    table.alert {
      background: $table-head-bg-alert;
    }
    table.compact {
      @include compact();
    }
    table.alternate {
      @include alternate();
    }

    table.table.striped {
      &:not(.expandable) {
        & > tbody > tr.even,
        & > tbody > tr.alt,
        & > tbody > tr:nth-of-type(even) {
          background-color: $table-even-row-bg;
          &:hover {
            background-color: $table-hover-bg-color;
          }
        }
        &.no-hover {
          & > tbody > tr.even,
          & > tbody > tr.alt,
          & > tbody > tr:nth-of-type(even) {
            background-color: $table-even-row-bg;
          }
        }
      }
      &.expandable {
        & > tbody:nth-of-type(even) > tr:nth-of-type(odd) {
          background-color: $table-even-row-bg;
          &:hover {
            background-color: $table-hover-bg-color;
          }
        }
        &.no-hover {
          & > tbody:nth-of-type(even) > tr:nth-of-type(odd):hover {
            background-color: $table-even-row-bg;
          }
        }
      }
    }
    table.table.no-hover {
      & > tbody > tr:hover, & > tbody:nth-of-type(even) > tr:nth-of-type(odd):hover {
        background-color: inherit;
      }
    }
    table.table.expandable > tbody {
      tr:not(:first-of-type) {
        background-color: $color-white;
        & td {
          padding: 1rem 2rem 1rem 2rem; //Give the expanded row a little more padding
        }
        &.recessed {
          background: linear-gradient(to bottom, rgba(96, 96, 96, .8) 0%, rgba(96, 96, 96, .2) 2%, rgba(240, 240, 240, .2) 18%, rgba(240, 240, 240, .2) 95%, rgba(96, 96, 96, .2) 98%, rgba(0, 0, 0, 0.2) 100%);
          background-color: #fcfcfc;
        }
      }
    }

    //Styling for default tables
    table:not(.no-style) {
      @extend table.table;

      //For nested tables
      table:not(.no-style) {
        margin: auto;
        &:not(.table) {
          @extend table.compact;
          @extend table.alternate;
        }
      }
    }
  }
}
