@import "global";
@import "buttons";
@import "controls";

//
// @variables
//
$include-html-form-classes: $include-html-classes !default;
$include-html-abide-classes: $include-html-form-classes !default;

// We use these to style the fieldset border and spacing.
$fieldset-border-style: solid !default;
$fieldset-border-width: 1px !default;
$fieldset-border-color: $hr-border-color !default;
$fieldset-padding: rem-calc(20) !default;
$fieldset-margin: $control-spacing rem-calc(0) !default;

// We use these to style the legends when you use them
$legend-bg: inherit !default;
$legend-text-color: $body-text-color !default;
//$legend-font-weight: $font-weight-normal !default;
//$legend-font-family: $font-family-sans-serif !default;
$legend-padding: rem-calc(0 3) !default;

// We use these to style the prefix and postfix input elements
$input-prefix-bg: $color-gray-20 !default;
$input-prefix-border-color: $color-gray-20 !default;
$input-prefix-border-size: 1px !default;
$input-prefix-border-type: dashed !default;
$input-prefix-overflow: hidden !default;
$input-prefix-font-color: $color-gray-90 !default;
$input-prefix-font-color-alt: $color-white !default;

// We use these to style the error states for inputs and labels
$input-error-message-padding: rem-calc(6 9 9) !default;
$input-error-message-top: -1px !default;
$input-error-message-font-size: rem-calc(12) !default;
//$input-error-message-font-weight: $font-weight-normal !default;
//$input-error-message-font-style: italic !default;
$input-error-message-font-color: $color-white !default;
$input-error-message-font-color-alt: $color-gray-90 !default;

// $field-state Icon
$field-state-border:  1px solid scale-color($color-6, $lightness: 50%);
$field-state-box-shadow: 0 0 5px $color-6-light;
$field-state-icon-color: $color-6-light;
$field-state-icon-size: $font-size-large;

// $field-state
$field-state-tooltip-font-size: $font-size-small;
//$field-state-tooltip-font-weight: $font-weight-bold;
//$field-state-font-weight: $font-weight-normal !default;
$field-state-font-color: $white !default;
$field-state-bg: $color-4 !default;
$field-state-padding: rem-calc(12) !default;

//
// @MIXINS
//

// We use this mixin to give us form styles for rows inside of forms
@mixin form-row-base {
  .row { margin: 0 ((-$control-spacing) / 2);

    .column,
    .columns { padding: 0 $control-spacing / 2; }

    // Use this to collapse the margins of a form row
    &.collapse { margin: 0;

      .column,
      .columns { padding: 0; }
      input {
        @include side-radius($opposite-direction, 0);
      }

    }
  }
  input.column,
  input.columns,
  textarea.column,
  textarea.columns { padding-#{$default-float}: $control-spacing / 2; }
}

// We use this mixin to style fieldsets
@mixin fieldset {
  border: $fieldset-border-width $fieldset-border-style $fieldset-border-color;
  padding: $control-spacing $fieldset-padding 0 $fieldset-padding;
  margin: 0 0 $control-spacing 0;

  // and legend styles
  legend {
    @extend .regular;
    background: $legend-bg;
    color: $legend-text-color;
    padding: 0;
    margin: 0;
    margin-#{$default-float}: rem-calc(-3);
  }
}

// @MIXIN
//
// We use this mixin to control border and background color of error inputs
// $color - Default: $alert-color (found in settings file)
@mixin form-error-color($color:$alert-color) {
  border-color: $color;
  background-color: rgba($color, 0.1);

  // Go back to normal on focus
  &:focus {
    background: $input-focus-bg-color;
    border-color: $input-focus-border-color;
  }
}

@mixin error_class($class, $message:null, $icon-font: 'snap-icons', $icon-class: null, $icon-char: null, $min-width: 10rem) {

  // If the input has the passed-in class, give it a red border and shadow
  input.#{$class}, input.#{$class}:focus {
    border: $field-state-border;
    box-shadow: $field-state-box-shadow;
  }
  // The input and the icon-containing element which follows it must BOTH
  // have the error class, or the icon doesn't display
  input:not(.#{$class}) + .#{$class} {
    display: none;
  }
  input.#{$class} {
    padding-right: 2.375rem !important;
    /* foundation styles inputs to be 100% width, display: block */
    /* if we were dealing with a inline-block input, we'd leave this off */
    float: left;
  }

  // On the icon-containing span...
  input.#{$class}+.#{$class} {

    //Include icon font/char if provided
    @if $icon-class or $icon-char {
      font-family: $icon-font;
      /*noinspection CssUnknownProperty*/
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;

      // Better Font Rendering
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;

      // Icons can be passed in by class (icon-barcode, e.g.) or their char ("\e04d", e.g.)
      @if $icon-class { @extend .#{$icon-class}; };
      @if $icon-char { &::before { content: $icon-char; } };
    }

    //Setup the span which holds the icon
    & {
      color: $field-state-icon-color;
      font-size: $input-font-size;
      display: inline-block;

      /* foundation styles inputs to be 100% width, display: block */
      /* if we were dealing with a inline-block input, we'd leave this off */
      float: left;
      /* Use negative margins to position us inside the input */
      width: 2.375rem;
      margin-left: -2.375rem;
      margin-top: .45rem;
      padding-left: .5rem;
      padding-right: .5rem;
      position: relative;
      cursor: $cursor-default-value;
    }

    //Setup the "tooltip"
    & {
      // Allow a message inside the span/div/whatever, as the first child element
      // This will only work correctly if the class was created with a null (default) message
      &>:first-child {display:none}

      //default messages, and messages in "text" attributes are displayed in ::after,
      //otherwise use the first child for the "tooltip"
      &:hover::after, &:hover>:first-child {
        @extend .regular;
        font-size: $field-state-tooltip-font-size;
        color: $field-state-font-color;
        display:block;
        position: absolute;
        line-height: 1.5;
        z-index: 1000;
        background: $field-state-bg;
        border: 1px solid $color-3;
        padding: $field-state-padding;
        @if $min-width {
          min-width: $min-width;
          max-width: 20rem;
        }
      }

      //default to "tooltip" on the right of the icon
      &:not(.west-side) {
        &:hover::after, &:hover>:first-child {
          left: 1rem;
          top: 1.15rem;
        }
      }
      //if the box is close to the screen edge, left of the icon may make more sense
      &.west-side {
        &:hover::after, &:hover>:first-child {
          right: 1.3rem;
          top: 1.15rem;
        }
      }

      @if $message {
        &:hover::after {
          content: $message;
        }
      }

      // A text attribute will always override the message, null or otherwise
      // A child div will ALWAYS display, if present
      &[text]:hover::after {
        content: attr(text);
      }
    }
  }
}

// Takes these parameters -> $class, $message:null, $icon-font: 'snap-icons', $icon-class: null, $icon-char: null, $min-width: 10rem
//@include error_class(error-required, 'Please enter a value for this field.', $icon-class: icon-question-dark);
//@include error_class(error-invalid, $icon-class: "icon-warning-dark");
//@include error_class(error-info, $icon-class: "icon-warning-dark");




// @MIXIN
//
// We use this mixin to create error message styles
// $bg - Default: $alert-color (Found in settings file)
@mixin form-error-message($bg:$alert-color) {
  display: block;
  padding: $input-error-message-padding;
  margin-top: $input-error-message-top;
  margin-bottom: $control-spacing;
  font-size: $input-error-message-font-size;
  font-weight: $input-error-message-font-weight;
  font-style: $input-error-message-font-style;

  // We can control the text color based on the brightness of the background.
  $bg-lightness: lightness($bg);
  background: $bg;
  @if $bg-lightness < 70% or $bg == yellow { color: $input-error-message-font-color; }
  @else { color: $input-error-message-font-color-alt; }
}

@include exports("form") {
  @if $include-html-form-classes {
    /* Standard Forms */
    form { margin: 0 0 $control-spacing; }

    /* Using forms within rows, we need to set some defaults */
    form .row { @include form-row-base; }

    form {
      .row {
        .prefix-radius.row.collapse {
          input,
          textarea,
          select { @include radius(0); @include side-radius($opposite-direction, $button-radius); }
          .prefix { @include radius(0); @include side-radius($default-float, $button-radius); }
        }
        .postfix-radius.row.collapse {
          input,
          textarea,
          select { @include radius(0); @include side-radius($default-float, $button-radius); }
          .postfix { @include radius(0); @include side-radius($opposite-direction, $button-radius); }
        }
        .prefix-round.row.collapse {
          input,
          textarea,
          select { @include radius(0); @include side-radius($opposite-direction, $button-round); }
          .prefix { @include radius(0); @include side-radius($default-float, $button-round); }
        }
        .postfix-round.row.collapse {
          input,
          textarea,
          select { @include radius(0); @include side-radius($default-float, $button-round); }
          .postfix { @include radius(0); @include side-radius($opposite-direction, $button-round); }
        }
      }
    }

    /* We add basic fieldset styling */
    fieldset {
      @include fieldset;
    }

    /* Error Handling */
    @if $include-html-abide-classes {
      #{data('abide')} {
        .error small.error, .error span.error, span.error, small.error {
          @include form-error-message;
        }
        span.error, small.error { display: none; }
      }

      span.error, small.error {
        @include form-error-message;
      }

      .error {
        input,
        textarea,
        select {
          margin-bottom: 0;
        }

        input[type="checkbox"],
        input[type="radio"] {
          margin-bottom: $control-spacing
        }

        label,
        label.error {
          @include label-error-color;
        }

        small.error {
          @include form-error-message;
        }

        > label {
          > small {
            color: $label-font-color; //            COLOR
            background: transparent;
            padding: 0;
            text-transform: $label-small-transform;
            font-style: normal;
            font-size: 60%;
            margin: 0;
            display: inline;
          }
        }

        span.error-message {
          display: block;
        }
      }

      input.error,
      textarea.error,
      select.error {
        margin-bottom: 0;
      }
      label.error { @include label-error-color; }
    }
  }
}
