@import "global";
@import "buttons";

//
// Button Group Variables
//
$include-html-button-classes: $include-html-classes !default;

// Sets the margin for the right side by default, and the left margin if right-to-left direction is used
$button-bar-margin-opposite: rem-calc(10) !default;
$button-group-border-width: 1px !default;
// The following variable was added to support enabled/disabled styling in button groups.
$button-group-margin: 0px !default;
$button-group-margin: 0 0;
//
// Button Group Mixins
//

// We use this to add styles for a button group container
@mixin button-group-container($styles:true, $float:false) {
  @if $styles {
    list-style-type: none;
    margin: 0;
    #{$default-float}: 0;
    @include clearfix();
  }
  @if $float {
    float: #{$default-float};
    margin-#{$opposite-direction}: $button-bar-margin-opposite;
    & div { overflow: hidden; }
  }
}

// We use this to control styles for button groups
@mixin button-group-style($radius:false, $even:false, $float:left, $orientation:horizontal) {

  > button, > .button {
    border-#{$default-float}: $button-group-border-width solid;
    margin-#{$default-float}:1px;
    &:focus {
      outline: none;
    }
  }

  &:first-child {
    button, .button {
      border-#{$default-float}: 0;
      margin-#{$default-float}: 0;
    }
  }

  // We use this to control the flow, or remove those styles completely.
  @if $float {
    margin: 0;
    float: $float;
    display: list-item;
    // Make sure the first child doesn't get the negative margin.
    &:first-child { margin-#{$default-float}: 0; }
  }
  @else {
    margin: $button-group-margin;
    display: inline-block;
  }

  @if $orientation == vertical {
    display:block;
    margin:0;
    > button, > .button {
      border-left: none;
      border-top: 1px solid;
      margin:1px 0 0 0;
      display: block;
    }

    &:first-child {
      button, .button {
        border-top: 0;
      }
    }
  }

  // We use these to control left and right radius on first/last buttons in the group.
  @if $radius == true {
    &,
    & > a,
    & > button,
    & > .button { @include radius(0); }
    &:first-child,
    &:first-child > a,
    &:first-child > button,
    &:first-child > .button {
      @if $orientation == vertical {
        @include side-radius(top, $button-radius);
      }
      @else {
        @include side-radius($default-float, $button-radius);
      }
    }
    &:last-child,
    &:last-child > a,
    &:last-child > button,
    &:last-child > .button {
      @if $orientation == vertical {
        @include side-radius(bottom, $button-radius);
      }
      @else {
        @include side-radius($opposite-direction, $button-radius);
      }
    }
  }
  @else if $radius {
    &,
    & > a,
    & > button,
    & > .button { @include radius(0); }
    &:first-child,
    &:first-child > a,
    &:first-child > button,
    &:first-child > .button {
      @if $orientation == vertical {
        @include side-radius(top, $radius);
      }
      @else {
        @include side-radius($default-float, $radius);
      }
    }
    &:last-child,
    &:last-child > a,
    &:last-child > button,
    &:last-child > .button {
      @if $orientation == vertical {
        @include side-radius(bottom, $radius);
      }
      @else {
        @include side-radius($opposite-direction, $radius);
      }
    }
  }

  // We use this to make the buttons even width across their container
  @if $even {
    width: percentage((100/$even) / 100);
    button, .button {
      width: calc(100% - 1px);
    }
    &:first-child button, &:first-child .button{
      width: 100%;
    }
  }
}

@include exports("button-group") {
  @if $include-html-button-classes {
    .button-group {
      @include button-group-container;

      & > li { @include button-group-style(); }

      &.row {
        li {
          &>button, &>.button {
            @include button-size($padding:null,$full-width:true);
          }
        }
      }

      &.stack {
        & > li {
          @include button-group-style($orientation:vertical, $float: none);
          & > button, & > .button {
            @include button-size($padding:null,$full-width:true);
          }
        }
      }

      &.stack-for-small {
        & > li {
          @include button-group-style($orientation:horizontal);
          @media #{$small-only} {
            @include button-group-style($orientation:vertical, $float: none);
            & > button, & > .button {
              @include button-size($padding: null, $full-width: true);
            }
          }
        }
      }

      &.radius > * { @include button-group-style($radius:$button-radius, $float:null); }
      &.radius.stack > * { @include button-group-style($radius:$button-radius, $float:null, $orientation:vertical); }
      &.radius.stack-for-small > * {
        @media #{$medium-up} {
          @include button-group-style($radius:$button-radius, $orientation:horizontal);
        }
        @media #{$small-only} {
          @include button-group-style($radius:$button-radius, $orientation:vertical);
        }
      }

      &.round > * { @include button-group-style($radius:$button-round, $float:null); }
      &.round.stack > * { @include button-group-style($radius:$button-med, $float:null, $orientation:vertical); }
      &.round.stack-for-small > * {
        @media #{$medium-up} {
          @include button-group-style($radius:$button-round, $orientation:horizontal);
        }
        @media #{$small-only} {
          @include button-group-style($radius:$button-med, $orientation:vertical);
        }
      }

      @for $i from 2 through 12 {
        &.even-#{$i}
        {
          &.stack-for-small>li {
            &>button, &>.button {
              @include button-size($padding:null,$full-width:true);
            }
          }
          li { @include button-group-style($even:$i, $float:null); }
        }
      }
    }

    .button-bar {
      @include clearfix;
      .button-group { @include button-group-container($styles:false, $float:true); }
    }
  }
}
