@import "global";
@import "grid";
@import "buttons";
@import "forms";

//
// Top Bar Variables
//
$include-html-top-bar-classes: $include-html-classes !default;

// Background color for the top bar
$topbar-bg-color: $action-bg-color !default;

// Height and margin
$topbar-height: 50px !default;
$topbar-margin-bottom: 0 !default;

// Controlling the styles for the title in the top bar
$topbar-title-font-size: rem-calc(17) !default;

// Set the link colors and styles for top-level nav
$topbar-link-color: $anchor-text-color-alt !default;
$topbar-link-color-hover: $anchor-text-color-alt !default;
$topbar-link-color-active: $anchor-text-color-alt !default;
$topbar-link-color-active-hover: $anchor-text-color-alt !default;
$topbar-link-font-size: rem-calc(14) !default;
$topbar-link-hover-lightness: -10% !default; // Darken by 10%
$topbar-link-bg: $topbar-bg-color !default;
//$topbar-link-bg-color-hover: #272727 !default;
$topbar-link-bg-color-hover: $action-bg-color-hover !default;
$topbar-link-bg-active: $action-bg-color !default;
$topbar-link-bg-active-hover: $action-bg-color-hover !default;
$topbar-link-text-transform: none !default;
$topbar-link-padding: $topbar-height / 3 !default;
$topbar-back-link-size: rem-calc(18) !default;
$topbar-link-dropdown-padding: 20px !default;
$topbar-button-font-size: 0.75rem !default;
$topbar-button-top: 7px !default;

// Style the top bar dropdown elements
$topbar-dropdown-bg-color: $action-bg-color !default;
$topbar-dropdown-link-color: $anchor-text-color-alt !default;
$topbar-dropdown-link-color-hover: $anchor-text-color-alt !default;
//$topbar-dropdown-link-bg-color: $action-bg-color !default;
$topbar-dropdown-link-bg-color: $color-3 !default;
$topbar-dropdown-link-bg-color-hover: $color-4-light !default;

$topbar-dropdown-toggle-size: 5px !default;
$topbar-dropdown-toggle-color: $anchor-text-color-alt !default;
$topbar-dropdown-toggle-alpha: 0.4 !default;

$topbar-dropdown-label-color: $body-text-color-alt !default;
$topbar-dropdown-label-text-transform: uppercase !default;
//$topbar-dropdown-label-font-weight: $font-weight-bold !default;
$topbar-dropdown-label-font-size: rem-calc(10) !default;
$topbar-dropdown-label-bg-color: $action-bg-color !default;
$topbar-dropdown-line-height: $topbar-height !default;
$topbar-dropdown-link-padding: 0 $topbar-link-padding !default;

// Top menu icon styles
$topbar-menu-link-transform: uppercase !default;
$topbar-menu-link-font-size: rem-calc(13) !default;
//$topbar-menu-link-weight: $font-weight-bold !default;
$topbar-menu-link-color: $anchor-text-color-alt !default;
$topbar-menu-icon-color: $anchor-text-color-alt !default;
$topbar-menu-link-color-toggled: $anchor-text-color-alt !default;
$topbar-menu-icon-color-toggled: $anchor-text-color-alt !default;

// Transitions and breakpoint styles
$topbar-transition-speed: $global-hover-transition-time !default;
// Using rem-calc for the below breakpoint causes issues with top bar
$topbar-breakpoint: #{lower-bound($medium-range)} !default; // Change to 9999px for always mobile layout
$topbar-media-query: $medium-up !default;

// Divider Styles
$topbar-divider-border-bottom: solid 1px scale-color($topbar-bg-color, $lightness: 25%) !default;
$topbar-divider-border-top: solid 1px scale-color($topbar-bg-color, $lightness: -50%) !default;

// Sticky Class
$topbar-sticky-class: ".sticky" !default;
$topbar-arrows: true !default; //Set false to remove the triangle icon from the menu item
$topbar-dropdown-arrows: true !default; //Set false to remove the \00bb >> text from dropdown subnavigation li

// Accessibility mixins for hiding and showing the menu dropdown items
@mixin topbar-hide-dropdown {
  // Makes an element visually hidden by default, but visible when focused.
  display: block;
  @include element-invisible();
}

@mixin topbar-show-dropdown {
  display: block;
  @include element-invisible-off();
  position: absolute !important; // Reset the position from static to absolute
}
// Snap team wrote this mixin to be called from masthead.scss. The top-bar doesn't use this mixin. Ironic, isn't it?
@mixin top-bar-config($bg-color: $color-4, $color: $body-text-color, $menu-line-height: false) {
  background: $bg-color;
  .top-bar-section {
    li:not(.has-form) {
      a:not(.button) {
        background: $bg-color;
      }
      &:not(.has-dropdown) {
        position: relative;
        cursor: pointer;
        & > span.active-marker {
          color: $color-4;
          display: block;
          position: absolute;
          top: -9px;
          left: 43%;
          @extend .glyph-medium;
          @extend .icon;
          @extend .icon-caret-down;
        }
      }
    }
    ul li > a {
      color: $color;
      font-size: 0.875rem;
    }
    li ul li:not(.has-form):not(.active) > a:not(.button) {
      line-height: 0.875rem;
      @if $menu-line-height {
        line-height: $menu-line-height;
      }
    }
    li:not(.has-form):not(.active):hover {
      & > a:not(.button) {
        color: $color;
        background-color: scale-color($action-bg-color, $lightness: 80%);
      }
    }
    li:not(.has-form):not(.active) > a:not(.button) {
      color: $color;
      @if $menu-line-height {
        line-height: $menu-line-height;
      }
    }
    li:not(.has-form) a:not(.button):hover, li:not(.has-form) a:not(.button):focus {
      background: scale-color($action-bg-color, $lightness: 80%);
      background-color: scale-color($action-bg-color, $lightness: 80%);
    }
    .dropdown li:not(.has-form):not(.active) > a:not(.button) {
      background-color: scale-color($action-bg-color, $lightness: 90%);
    }
    .dropdown li:not(.has-form):not(.active):hover > a:not(.button) {
        background-color: scale-color($action-bg-color, $lightness: 80%);
    }
    .has-dropdown {
        & > a {
          &:after {
            @include css-triangle($topbar-dropdown-toggle-size, rgba($color, $topbar-dropdown-toggle-alpha), top);
          }
        }
    }
  }
}

@include exports("top-bar") {

  @if $include-html-top-bar-classes {

    // Used to provide media query values for javascript components.
    // This class is generated despite the value of $include-html-top-bar-classes
    // to ensure width calculations work correctly.
    meta.foundation-mq-topbar {
      font-family: "/" + unquote($topbar-media-query) + "/";
      width: $topbar-breakpoint;
    }

    /* Wrapped around .top-bar to contain to grid width */
    .contain-to-grid {
      width: 100%;
      background: $topbar-bg-color;

      .top-bar { margin-bottom: $topbar-margin-bottom; }
    }

    // Wrapped around .top-bar to make it stick to the top
    .fixed {
      width: 100%;
      #{$default-float}: 0;
      position: fixed;
      top: 0;
      z-index: 99;

      &.expanded:not(.top-bar) {
          overflow-y: auto;
          height: auto;
          width: 100%;
          max-height: 100%;

        .title-area {
          position: fixed;
          width: 100%;
          z-index: 99;
        }
        // Ensure you can scroll the menu on small screens
        .top-bar-section {
          z-index: 98;
          margin-top: $topbar-height;
        }
      }
    }

    .top-bar {
      overflow: hidden;
      height: $topbar-height;
      line-height: $topbar-height;
      position: relative;
      background: $topbar-bg-color;
      margin-bottom: $topbar-margin-bottom;

      // Topbar Global list Styles
      ul {
        margin-bottom: 0;
        list-style-type: none;
      }
      .row { max-width: none; }

      form,
      input { margin-bottom: 0; }

      input { height: 1.8rem; padding-top: .35rem; padding-bottom: .35rem; font-size: $topbar-button-font-size; }

      .button, button {
        padding-top: .35rem + rem-calc(1);
        padding-bottom: .35rem + rem-calc(1);
        margin-bottom: 0;
        font-size: $topbar-button-font-size;
          // position: relative;
          // top: -1px;

        // Corrects a slight misalignment when put next to an input field
        @media #{$small-only} {
          position: relative;
          top: -1px;
        }
      }

      // Title Area
      .title-area {
        position: relative;
        margin: 0;
      }

      .name {
        height: $topbar-height;
        margin: 0;
        font-size: $rem-base;

        h1 {
          line-height: $topbar-height;
          font-size: $topbar-title-font-size;
          margin: 0;
          a {
            color: $topbar-link-color;
            width: 75%;
            display: block;
            padding: 0 $topbar-link-padding;
          }
        }
      }

      // Menu toggle button on small devices
      .toggle-topbar {
        position: absolute;
        #{$opposite-direction}: 0;
        top: 0;

        a {
          color: $topbar-link-color;
          text-transform: $topbar-menu-link-transform;
          font-size: $topbar-menu-link-font-size;
          position: relative;
          display: block;
          padding: 0 $topbar-link-padding;
          height: $topbar-height;
          line-height: $topbar-height;
        }

        // Adding the class "menu-icon" will add the 3-line icon people love and adore.
        &.menu-icon {
          top: 50%;
          margin-top: -16px;

          a {
            @if $text-direction == rtl {
              text-indent: -58px;
            }
            height: 34px;
            line-height: 33px;
            padding: 0 $topbar-link-padding+25 0 $topbar-link-padding;
            color: $topbar-menu-link-color;
            position: relative;

            & {
              // @include hamburger icon
              //
              // We use this to create the icon with three lines aka the hamburger icon, the menu-icon or the navicon
              // $width - Width of hamburger icon
              // $left - If false, icon will be centered horizontally || explicitly set value in rem
              // $top - If false, icon will be centered vertically || explicitly set value in rem
              // $thickness - thickness of lines in hamburger icon, set value in px
              // $gap - spacing between the lines in hamburger icon, set value in px
              // $color - icon color
              // $hover-color - icon color during hover, here it isn't set b/c it would override $topbar-menu-icon-color-toggled
              // $offcanvas - Set to false of @include in topbar
              @include hamburger(16px, false, 0, 1px, 6px, $topbar-menu-icon-color, false, false);
            }
          }
        }
      }

      // Change things up when the top-bar is expanded
      &.expanded {
        height: auto;
        background: transparent;

        .title-area { background: $topbar-bg-color; }

        .toggle-topbar {
          a { color: $topbar-menu-link-color-toggled;
			span::after {
              // Shh, don't tell, but box-shadows create the menu icon :)
              // Change the color of the bars when the menu is expanded, using given thickness from hamburger() above
              box-shadow: 0 0px 0 1px $topbar-menu-icon-color-toggled,
                          0 7px 0 1px $topbar-menu-icon-color-toggled,
                          0 14px 0 1px $topbar-menu-icon-color-toggled;
            }
          }
        }
      }
    }

    // Right and Left Navigation that stacked by default
    .top-bar-section {
      #{$default-float}: 0;
      position: relative;
      width: auto;
      @include single-transition($default-float, $topbar-transition-speed);

      ul {
        padding: 0;
        width: 100%;
        height: auto;
        display: block;
        font-size: $rem-base;
        margin: 0;
      }

      .divider,
      [role="separator"] {
        border-top: $topbar-divider-border-top;
        clear: both;
        height: 1px;
        width: 100%;
      }

      ul li {
        background: $topbar-dropdown-bg-color;
        & > a {
          display: block;
          width: 100%;
          &:not(.button) {
            color: $topbar-link-color;
          }
          padding: 12px 0 12px 0;
          padding-#{$default-float}: $topbar-link-padding;
          font-size: $topbar-link-font-size;
          text-transform: $topbar-link-text-transform;

            //removed button-style because we weren't using buttons in top bar and we need to keep :hovers down
            //  may do the right thing by default anyway (once someone figures out what the "right" thing is)
          &.button {
            font-size: $topbar-link-font-size;
            padding-#{$opposite-direction}: $topbar-link-padding;
            padding-#{$default-float}: $topbar-link-padding;
//            @include button-style($bg:$action-bg-color);
          }
//          &.button.secondary { @include button-style($bg:$action-bg-color-alt); }
//          &.button.success { @include button-style($bg:$color-success); }
//          &.button.alert { @include button-style($bg:$color-error); }
//          &.button.warning { @include button-style($bg:$color-attention); }
        }

        //removed button-style because we weren't using buttons in top bar and we need to keep :hovers down
        //  may do the right thing by default anyway (once someone figures out what the "right" thing is)
        > button {
          font-size: $topbar-link-font-size;
          padding-#{$opposite-direction}: $topbar-link-padding;
          padding-#{$default-float}: $topbar-link-padding;
//          @include button-style($bg:$action-bg-color);

//          &.secondary { @include button-style($bg:$action-bg-color-alt); }
//          &.success { @include button-style($bg:$color-success); }
//          &.alert { @include button-style($bg:$color-error); }
//          &.warning { @include button-style($bg:$color-attention); }
        }

        // Apply the hover link color when it has that class
        &:hover:not(.has-form) > a {
          background-color: $topbar-link-bg-color-hover;
          @if ($topbar-link-bg-color-hover) {
            background: $topbar-link-bg-color-hover;
          }
          color: $topbar-link-color-hover;

        }

        // Apply the active link color when it has that class
        &.active > a {
          background: $topbar-link-bg-active;
          color: $topbar-link-color-active;
          &:hover {
                background: $topbar-link-bg-active-hover;
                color: $topbar-link-color-active-hover;
          }
        }
      }

      // Add some extra padding for list items contains buttons
      .has-form { padding: $topbar-link-padding; }

      // Styling for list items that have a dropdown within them.
      .has-dropdown {
        position: relative;

        & > a {
          &:after {
            @if ($topbar-arrows){
              @include css-triangle($topbar-dropdown-toggle-size, rgba($topbar-dropdown-toggle-color, $topbar-dropdown-toggle-alpha), $default-float);
            }
            margin-#{$opposite-direction}: $topbar-link-padding;
            margin-top: -($topbar-dropdown-toggle-size / 2) - 2;
            position: absolute;
            top: 50%;
            #{$opposite-direction}: 0;
          }
        }

        &.moved { position: static;
          & > .dropdown {
            @include topbar-show-dropdown();
            width: 100%;
          }
          & > a:after {
            display: none;
          }
        }
      }

      // Styling elements inside of dropdowns
      .dropdown {
        padding: 0;
        position: absolute;
        #{$default-float}: 100%;
        top: 0;
        z-index: 99;
        @include topbar-hide-dropdown();

        li {
          width: 100%;
          height: auto;

          a {
            padding: 8px $topbar-link-padding;
          }

          &.title h5, &.parent-link {
            // Back Button
            margin-bottom: 0;
            margin-top: 0;
            font-size: $topbar-back-link-size;
            a {
              color: $topbar-link-color;
              // line-height: $topbar-height / 2;
              display: block;
              &:hover { background:none; }
            }
          }
          &.has-form { padding: 8px $topbar-link-padding; }
          .button, button { top: auto; }
        }

        label {
          padding: 8px $topbar-link-padding 2px;
          margin-bottom: 0;
          text-transform: $topbar-dropdown-label-text-transform;
          color: $topbar-dropdown-label-color;
          font-size: $topbar-dropdown-label-font-size;
        }
      }
    }

    .js-generated { display: block; }


    // Top Bar styles intended for screen sizes above the breakpoint.
    @media #{$topbar-media-query} {
      .top-bar {
        background: $topbar-bg-color;
        @include clearfix;
        overflow: visible;

        .toggle-topbar { display: none; }

        .title-area { float: $default-float; }
        .name h1 a { width: auto; }

        input,
        .button,
        button {
          font-size: rem-calc(14);
          position: relative;
          top: $topbar-button-top;
        }

        &.expanded { background: $topbar-bg-color; }
      }

      .contain-to-grid .top-bar {
        max-width: $row-width;
        margin: 0 auto;
        margin-bottom: $topbar-margin-bottom;
      }

      .top-bar-section {
        @include single-transition;
        #{$default-float}: 0 !important;

        ul {
          width: auto;
          height: auto !important;
          display: inline;

          li {
            float: $default-float;
            .js-generated { display: none; }
          }
        }

        li {
          &.hover {
            > a:not(.button) {
              background-color: $topbar-link-bg-color-hover;
              @if ($topbar-link-bg-color-hover) {
                background: $topbar-link-bg-color-hover;
              }
              color: $topbar-link-color-hover;
            }
          }
          &:not(.has-form) {
            a:not(.button) {
              padding: 0 $topbar-link-padding;
              line-height: $topbar-height;
              background: $topbar-link-bg;
              &:hover {
                background-color: $topbar-link-bg-color-hover;
                @if ($topbar-link-bg-color-hover) {
                  background: $topbar-link-bg-color-hover;
                }
              }
            }
          }
          &.active:not(.has-form) {
            a:not(.button) {
              padding: 0 $topbar-link-padding;
              line-height: $topbar-height;
              color: $topbar-link-color-active;
              background: $topbar-link-bg-active;
              &:hover {
                background: $topbar-link-bg-active-hover;
                color: $topbar-link-color-active-hover;
              }
            }
          }
        }

        .has-dropdown {

          @if($topbar-arrows){

            & > a {
              padding-#{$opposite-direction}: $topbar-link-padding + $topbar-link-dropdown-padding !important;
              &:after {
                @include css-triangle($topbar-dropdown-toggle-size, rgba($topbar-dropdown-toggle-color, $topbar-dropdown-toggle-alpha), top);
                margin-top: -($topbar-dropdown-toggle-size / 2);
                top: 50%;
              }
            }

          }

          &.moved { position: relative;
            & > .dropdown {
              @include topbar-hide-dropdown();
            }
          }

          &.hover, &.not-click:hover {
            & > .dropdown {
              @include topbar-show-dropdown();
            }
          }
          > a:focus + .dropdown {
            @include topbar-show-dropdown();
          }

          .dropdown li.has-dropdown {
            & > a {
            @if ($topbar-dropdown-arrows){
				&:after {
				border: none;
				content: "\00bb";
				top: 1rem;
				margin-top: -1px;
				#{$opposite-direction}: 5px;
				line-height: 1.2;
				}
              }
            }
          }

        }

        .dropdown {
          #{$default-float}: 0;
          top: auto;
          background: transparent;
          min-width: 100%;

          li {
            a, &:not(.has-form) a:not(.button)  {
              color: $topbar-dropdown-link-color;
              white-space: nowrap;
              padding: $topbar-dropdown-link-padding;
              line-height: $topbar-dropdown-line-height;
              background: $topbar-dropdown-link-bg-color;
            }

            &:not(.has-form):not(.active) {
              & > a:not(.button) {
                color: $topbar-dropdown-link-color;
                background: $topbar-dropdown-link-bg-color;
              }

              &:hover > a:not(.button) {
                color: $topbar-dropdown-link-color-hover;
                background-color: $topbar-link-bg-color-hover;
                @if ($topbar-link-bg-color-hover) {
                  background: $topbar-link-bg-color-hover;
                }
              }
            }

            label {
              white-space: nowrap;
              background: $topbar-dropdown-label-bg-color;
            }

            // Second Level Dropdowns
            .dropdown {
              #{$default-float}: 100%;
              top: 0;
            }
          }
        }

        & > ul > .divider,
        & > ul > [role="separator"] {
          border-bottom: none;
          border-top: none;
          border-#{$opposite-direction}: $topbar-divider-border-bottom;
          clear: none;
          height: $topbar-height;
          width: 0;
        }

        .has-form {
          background: $topbar-link-bg;
          padding: 0 $topbar-height / 3;
          height: $topbar-height;
        }

        // Position overrides for ul.right and ul.left
        .#{$opposite-direction} {
          li .dropdown {
            #{$default-float}: auto;
            #{$opposite-direction}: 0;

            li .dropdown { #{$opposite-direction}: 100%; }
          }
        }
        .#{$default-float} {
          li .dropdown {
            #{$opposite-direction}: auto;
            #{$default-float}: 0;

            li .dropdown { #{$default-float}: 100%; }
          }
        }
      }

      // Degrade gracefully when Javascript is disabled. Displays dropdown and changes
      // background & text color on hover.
      .no-js .top-bar-section {
        ul li {
          // Apply the hover link color when it has that class
          &:hover > a {
            background-color: $topbar-link-bg-color-hover;
            @if ($topbar-link-bg-color-hover) {
              background: $topbar-link-bg-color-hover;
            }
            color: $topbar-link-color-hover;
          }

          // Apply the active link color when it has that class
          &:active > a {
            background: $topbar-link-bg-active;
            color: $topbar-link-color-active;
          }
        }

        .has-dropdown {
          &:hover {
            & > .dropdown {
              @include topbar-show-dropdown();
            }
          }
          > a:focus + .dropdown {
            @include topbar-show-dropdown();
          }
        }
      }
    }
  }

}
